@import '../../base.scss';
@import './mixins';

.hse-DropdownMenu {
  @include hse-DropdownMenu;
}

.hse-DropdownMenu_has-message.hse-DropdownMenu_searchable_dropdown {
  .hse-DropdownMenuMessage {
    margin: $spacing-small-3x 0 0;
  }

  .hse-DropdownMenu__items {
    padding-top: 0;
  }
}

.hse-DropdownMenu_has-message.hse-DropdownMenu_searchable_inline {
  .hse-DropdownMenu__items ~ .hse-DropdownMenuMessage {
    margin: $spacing-small-3x 0 0;
  }
}

.hse-DropdownMenu_empty {
  border-color: transparent;
}

.hse-DropdownMenu_searchable_dropdown {
  border: 1px solid $color-gray-dusk-5;
  border-radius: $radius-small;
}

.hse-DropdownMenu__header {
  padding: $spacing-small-2x;
}

.hse-DropdownMenu__items {
  @include hse-DropdownMenu__items;
}

.hse-DropdownMenu_empty {
  border-color: transparent;
}

.hse-DropdownMenu_borderless {
  border: none;

  .hse-DropdownMenu__items {
    padding: 0;
  }
}

.hse-DropdownMenuBaseItem {
  @include hse-DropdownMenuBaseItem;
}

.hse-DropdownMenuBaseItem__content {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: var(--DropdownMenuBaseItem-line-clamp);
}

.hse-DropdownMenuBaseItem:not(:last-child),
.hse-DropdownMenuDivider:not(:last-child),
.hse-DropdownMenuGroup:not(:last-child) {
  margin-bottom: $spacing-small-3x / 2;
}

.hse-DropdownMenuBaseItem:hover,
.hse-DropdownMenuBaseItem_hovered {
  background-color: $color-background-gray-1;
}

.hse-DropdownMenuBaseItem_selected {
  @include hse-DropdownMenuBaseItem_selected;
}

.hse-DropdownMenuBaseItem_selected:hover,
.hse-DropdownMenuBaseItem_selected.hse-DropdownMenuBaseItem_hovered {
  @include hse-DropdownMenuBaseItem_hover_selected;
}

.hse-DropdownMenuBaseItem_focused {
  @include hse-DropdownMenuBaseItem_focused;
}

.hse-DropdownMenuBaseItem_focused.hse-DropdownMenuBaseItem_selected {
  @include hse-DropdownMenuBaseItem_selected_focused;
}

.hse-DropdownMenuBaseItem_disabled {
  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    opacity: 0.8;
    border-radius: $radius-small;
    background-color: $color-background-white;
  }

  &:hover {
    background-color: inherit;
  }
}

.hse-DropdownMenuBaseItem__left {
  display: flex;
  align-items: center;
  margin-right: $spacing-small-3x;
}

.hse-DropdownMenuBaseItem__content {
  flex-grow: 1;
}

.hse-DropdownMenuBaseItem__right {
  margin-left: $spacing-small-3x;
}

.hse-DropdownMenuGroup {
  padding: $spacing-small-2x 0;
}

.hse-DropdownMenuGroup__title {
  @include text_caption_small_bold;
  box-sizing: border-box;
  height: $sizing-40;
  padding: $spacing-small-2x;
  color: $color-gray-dusk-3;
}

.hse-DropdownMenuDivider {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  height: $sizing-32;
}

.hse-DropdownMenuDivider__line {
  height: 1px;
  background-color: $color-gray-dusk-5;
}

.hse-DropdownMenuItem__badge {
  @include text_caption_x_small_bold;
  padding: $spacing-small-3x / 4 $spacing-small-3x;
  color: $color-base-white;
  border-radius: 2 * $radius-medium;
  background-color: $color-gray-dusk;
}

.hse-DropdownMenuItem__leftIcon,
.hse-DropdownMenuItem__rightIcon {
  color: $color-gray-dusk;
}

.hse-DropdownMenuItem_selected {
  .hse-DropdownMenuItem__badge {
    color: $color-brand;
    background-color: $color-base-white;
  }

  .hse-DropdownMenuItem__leftIcon,
  .hse-DropdownMenuItem__rightIcon {
    color: $color-base-white;
  }
}

.hse-DropdownMenuMessage {
  @include text_caption_medium_regular;
  z-index: $z-index-dropdown-menu-message;
  display: block;
  min-height: $sizing-40;
  padding: 10px $spacing-small-2x;
  text-align: center;
  color: $color-gray-dusk;
  border: 1px solid $color-gray-morn;
  border-radius: $radius-small;
  background: $color-gray-morn-4;
}
