@import '../../base';
@import './vars';
@import './mixins';

/// Тень focus-visible находится в ::before
/// Нижнее подчеркивание варианта line в ::after

.hse-TabsItem {
  @include hse-TabsItem;
}

.hse-TabsItem__counter {
  @include hse-TabsItem__counter;
}

.hse-TabsItem_selected {
  @include hse-TabsItem_selected;
}

.hse-TabsItem_disabled {
  @include hse-TabsItem_disabled;
}

/// Variants styles

/// line variant

/// default state
.hse-TabsItem_variant_line {
  @include hse-TabsItem_variant_line;
}

/// selected state
.hse-TabsItem_variant_line.hse-TabsItem_selected {
  @include hse-TabsItem_variant_line_selected;
}

/// hover
.hse-TabsItem_variant_line:not(.hse-TabsItem_disabled):hover {
  @include hse-TabsItem_variant_line_hover;
}

/// line + outlined

/// default
.hse-TabsItem_variant_line.hse-TabsItem_style_outlined {
  border: 1px solid $color-gray-dusk-3;
  border-bottom-color: transparent;
}

/// selected
.hse-TabsItem_variant_line.hse-TabsItem_style_outlined.hse-TabsItem_selected {
  border-color: $color-brand;
  border-top-color: transparent;

  &:focus-visible {
    border-top-color: inherit;
  }
}

/// line + filled

.hse-TabsItem_variant_line.hse-TabsItem_style_filled {
  background: $color-gray-morn-4;
}

.hse-TabsItem_variant_line.hse-TabsItem_style_filled.hse-TabsItem_selected {
  background: $color-background-white;
}

.hse-TabsItem_variant_line.hse-TabsItem_style_filled {
  &:focus-visible {
    &::before {
      border: $tabs-item-border-style;
    }
  }
}

.hse-TabsItem_variant_line.hse-TabsItem_style_filled:not(.hse-TabsItem_disabled):hover::before {
  border: $tabs-item-border-style;
}

/// square variant

.hse-TabsItem_variant_square {
  &:focus-visible {
    color: $color-brand;

    &::before {
      border: 1px solid currentColor;
    }
  }
}

.hse-TabsItem_variant_square,
.hse-TabsItem_variant_square::before {
  border-radius: $radius-small;
}

/// square + filled

.hse-TabsItem_variant_square.hse-TabsItem_style_filled {
  background: $color-gray-morn-4;
}

/// square + outlined

.hse-TabsItem_variant_square.hse-TabsItem_style_outlined::before {
  border: 1px solid currentColor;
}

/// selected

.hse-TabsItem_variant_square.hse-TabsItem_selected::before {
  border: 1px solid currentColor;
}

.hse-TabsItem_variant_square.hse-TabsItem_style_outlined.hse-TabsItem_selected {
  background: $color-brand-5;
}

.hse-TabsItem_variant_square.hse-TabsItem_style_normal.hse-TabsItem_selected {
  background: transparent;
}

.hse-TabsItem_variant_square.hse-TabsItem_style_filled.hse-TabsItem_selected {
  border-color: $color-brand;
  background: $color-gray-morn-4;
}

.hse-TabsItem_variant_square.hse-TabsItem_style_outlined.hse-TabsItem_selected::before {
  border-color: $color-brand;
}

.hse-TabsItem_variant_square.hse-TabsItem_style_normal:not(.hse-TabsItem_disabled):hover,
.hse-TabsItem_variant_square.hse-TabsItem_style_filled:not(.hse-TabsItem_disabled):hover,
.hse-TabsItem_variant_square.hse-TabsItem_selected {
  color: $color-brand;
}

/// round variant

/// selected

.hse-TabsItem_variant_round.hse-TabsItem_style_outlined::before,
.hse-TabsItem_variant_round.hse-TabsItem_style_filled::before {
  border: 1px solid currentColor;
}

.hse-TabsItem_variant_round.hse-TabsItem_style_outlined.hse-TabsItem_selected,
.hse-TabsItem_variant_round.hse-TabsItem_style_filled.hse-TabsItem_selected {
  background: $color-gray-morn-4;
}

.hse-TabsItem_variant_round.hse-TabsItem_style_normal:not(.hse-TabsItem_disabled):hover,
.hse-TabsItem_variant_round.hse-TabsItem_selected {
  color: $color-brand;
  background: $color-background-white;
}

.hse-TabsItem_variant_round.hse-TabsItem_style_filled:not(.hse-TabsItem_disabled):hover::before,
.hse-TabsItem_variant_round.hse-TabsItem_style_filled:focus-visible::before {
  border: $tabs-item-border-style;
}

.hse-TabsItem_variant_round,
.hse-TabsItem_variant_round::before {
  border-radius: $sizing-40;
}

.hse-TabsItem_variant_round.hse-TabsItem_style_normal:focus-visible,
.hse-TabsItem_variant_round.hse-TabsItem_style_outlined:focus-visible {
  color: $color-brand;
}

.hse-TabsItem_variant_round.hse-TabsItem_style_outlined:not(.hse-TabsItem_disabled):hover::before {
  border: $tabs-item-border-style;
}

/// round + filled

.hse-TabsItem_variant_round.hse-TabsItem_style_filled {
  background: $color-gray-morn-4;

  &:focus-visible {
    background: transparent;
  }
}
