// @define Loader

@import '../base';

$animation-duration: 1.4s;
$radius: '(50% - 1px)';

.hse-Loader {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: hse-Loader-animation $animation-duration linear infinite;

  color: $color-brand;
}

.hse-Loader_medium {
  width: $sizing-24;
  height: $sizing-24;
}
.hse-Loader_small {
  width: $sizing-16;
  height: $sizing-16;
}

.hse-Loader__svg {
  width: 100%;
  height: 100%;
}
.hse-Loader__circleBackground {
  stroke: $color-gray-morn;
  stroke-width: 2px;
}

.hse-Loader__circle {
  animation: hse-Loader-circle-animation $animation-duration ease-in-out
    infinite;
  stroke-dasharray: calc(2 * #{$pi} * #{$radius} * 0.8),
    calc(4 * #{$pi} * #{$radius});
  stroke-dashoffset: 0;
  stroke: currentColor;
  stroke-linecap: round;
  stroke-width: 2px;
}

@keyframes hse-Loader-animation {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes hse-Loader-circle-animation {
  0% {
    stroke-dasharray: 1px, calc(4 * #{$pi} * #{$radius});
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: calc(2 * #{$pi} * #{$radius} * 0.7),
      calc(4 * #{$pi} * #{$radius});
    stroke-dashoffset: calc(-2 * #{$pi} * #{$radius} * 0.12);
  }
  100% {
    stroke-dasharray: calc(2 * #{$pi} * #{$radius} * 0.7),
      calc(4 * #{$pi} * #{$radius});
    stroke-dashoffset: calc(-2 * #{$pi} * #{$radius} * 1 - 1px);
  }
}
