// @define Select

@import '../../base';

.hse-Select {
  $min-width: 280px;
  position: relative;
  align-items: center;
  width: $min-width;
  min-width: $min-width;
  user-select: none;
  border-radius: $radius-small;
  font: inherit;

  &:focus {
    outline: none;
  }
}

.hse-Select_fullWidth {
  width: auto;
  min-width: 100%;
}

.hse-Select_disabled {
  background-color: $color-gray-morn;
}

.hse-Select__valueContainer.hse-Select__valueContainer_disabled {
  .hse-Select__value,
  .hse-Icon.hse-Select__valueContainerIcon {
    color: $color-gray-dusk-4;
  }
}

.hse-Select__valueContainer.hse-Select__valueContainer_disabled
  .hse-Select__clearIcon {
  pointer-events: none;
}

.hse-Select_width_full {
  width: auto;
  min-width: 100%;
}

.hse-Select_width_auto {
  width: max-content;
  min-width: auto;
}

.hse-Select_width_auto .hse-DropdownMenuBaseItem,
.hse-Select_width_auto .hse-DropdownMenuGroup {
  white-space: nowrap;
}

.hse-Select__input {
  @include hideDefaultInput;
}

.hse-Select__input_small {
  @include text_caption_small_bold;
  height: $sizing-32;
}
.hse-Select__valueContainer {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  min-width: 100%;
  padding-right: $spacing-small-3x;
  padding-left: $spacing-small-2x;
  cursor: pointer;
  transition: background $transition-quick ease-in-out,
    color $transition-quick ease-in-out, border $transition-quick ease-in-out;
  color: $color-gray-dusk;
  border: 1px solid $color-gray-morn;
  border-radius: $radius-small;
  background: $color-base-white;
}

.hse-Select__valueContainer:hover {
  border-color: $color-brand;
}
.hse-Select__value {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  margin-right: $spacing-small-3x;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.hse-Select__valuePlaceholder {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  margin-right: $spacing-small-3x;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: $color-gray-dusk;
}

.hse-Select__valueContainerIcon {
  $size: $sizing-24;
  position: relative;
  width: $size;
  min-width: $size;
  height: $size;
  transition: $transition-quick transform ease-in-out,
    $transition-quick color ease-in-out;
}
.hse-Select__valueContainerIcon_opened {
  transform: rotate(180deg);
}
.hse-Select__valueContainer_selected:not(.hse-Select__valueContainer_disabled) {
  color: $color-base-black;
}
.hse-Select__valueContainer_size_small {
  @include text_caption_small_regular;
  height: $sizing-32;
}
.hse-Select__valueContainer_size_medium {
  @include text_caption_medium_regular;
  height: $sizing-40;
}

.hse-Select__valueContainer_size_large {
  @include text_caption_medium_regular;
  height: $sizing-48;
}

.hse-Select__valueContainer_focused {
  border-color: $color-brand;
}

.hse-Select__valueContainer_disabled {
  @include disabledState;
  cursor: default;

  &::before {
    border-radius: $radius-small;
  }
}

.hse-Select__clearIcon:not(.hse-ClickableIcon_focused) {
  color: $color-gray-dusk;
}

.hse-Select__valueContainer_disabled .hse-Select__multipleValuesEtc,
.hse-Select__valueContainer_disabled
  .hse-Select__clearIcon.hse-ClickableIcon
  .hse-Icon {
  color: $color-gray-dusk-3;
}

.hse-Select__valueContainer_error {
  border-color: $color-system-error;
}

.hse-Select__valueContainerLeftIcon {
  flex-shrink: 0;
  margin-right: $spacing-small-3x;
}

.hse-Select__multipleValues {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  flex-flow: row nowrap;
  flex-grow: 1;
  height: 30px;
  padding-left: 2px;
  white-space: nowrap;
}

.hse-Select__multipleValuesEtc {
  @include text_caption_medium_regular;
  color: $color-base-black;
  white-space: nowrap;
  margin-left: $spacing-small-3x / 2;
}

.hse-Select__multipleValue {
  max-width: 100%;
  white-space: nowrap;
}

.hse-Select__multipleValue span {
  overflow: hidden;
  text-overflow: ellipsis;
}

.hse-Select__multipleValue:not(:last-child) {
  margin-right: $spacing-small-3x / 2;
}

.hse-Select__multipleValue.hse-Select__multipleValue_disabled {
  color: $color-gray-dusk-3;
  background-color: rgba($color-element-overlay-light, $opacity-90);
}

.hse-Select__clearIcon {
  position: relative;
}

.hse-Select__value_multiple {
  display: flex;
  color: $color-base-white;
  background-color: $color-brand;
}

.hse-Select__valuesCounter {
  @include text_caption_x_small_bold;
  position: relative;
  display: flex;
  align-items: center;
  height: 20px;
  margin-right: $spacing-small-3x;
  margin-left: $spacing-small-x;
  padding: 0 $spacing-small-3x;
  color: $color-base-white;
  border-radius: 2 * $radius-medium;
  background-color: $color-brand;
  white-space: nowrap;
}

.hse-Select__valuesCounter_disabled {
  color: $color-gray-dusk-2;
  background-color: $color-gray-morn-2;
}

.hse-Select__searchInput {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr auto;
  &:focus {
    outline: none;
  }

  & ~ .hse-DropdownMenuMessage {
    margin-top: $spacing-small-3x;
  }
}
.hse-Select__searchInput_inline {
  padding: 0;
}

.hse-Select__searchInput_noBorder {
  border: none;
}

.hse-Select__searchIcon {
  color: $color-gray-dusk;
}

.hse-Select__menu {
  position: absolute;
  top: $spacing-small-3x;
  visibility: hidden;
  overflow: hidden;
  min-width: 100%;
  height: 0;
}

.hse-Select__menu_opened {
  visibility: visible;
  box-sizing: border-box;
  height: auto;
}

.hse-Select__optionGroupLabel {
  display: flex;
  min-width: 100%;
  height: $sizing-32;
  cursor: default;
  color: black;
  font-weight: bold;
}

.hse-Select__option {
  display: flex;
  box-sizing: border-box;
  min-width: 100%;
  height: $sizing-24;
  cursor: pointer;
  color: black;
  border: 1px solid black;
  background-color: transparent;
}

.hse-Select__option_focused {
  background-color: grey;
}

.hse-Select__option_selected {
  background-color: $color-brand;
}

.hse-Select__option_inactive {
  cursor: default;
  color: $color-gray-dusk;
  background-color: transparent;
}

.hse-Select_optionDivider {
  width: 100%;
}

// borderless variant styles

.hse-Select_variant_borderless.hse-Select,
.hse-Select_variant_borderless .hse-Select__valueContainer {
  border-color: transparent;
  background: transparent;
}

.hse-Select_variant_borderless .hse-Select__valueContainer {
  @include text_caption_small_bold;
}

.hse-Select_variant_borderless .hse-Select__valueContainer:hover,
.hse-Select_variant_borderless
  .hse-Select__valueContainer:hover
  .hse-Select__valueContainerIcon {
  color: $color-base-black;
  border-color: $color-gray-morn;
}

.hse-Select_variant_borderless .hse-Select__valueContainer,
.hse-Select_variant_borderless .hse-Select__valueContainerIcon {
  color: $color-gray-dusk;
}

.hse-Select_variant_borderless
  .hse-Select__valueContainer.hse-Select__valueContainer_selected,
.hse-Select_variant_borderless
  .hse-Select__valueContainer_selected
  .hse-Select__valueContainerIcon.hse-Icon {
  color: $color-base-black;
}

.hse-Select_variant_borderless .hse-Select__valueContainer_focused {
}

.hse-Select_variant_borderless.hse-Select_disabled {
  background: $color-gray-morn-3;
}

.hse-Select_variant_borderless.hse-Select_opened .hse-Select__valueContainer {
  color: $color-base-black;
  background: $color-gray-morn-3;
}

.hse-Select_variant_borderless:focus-visible {
  @include focus-shadow;
}
