@import '../../base';

.hse-FileButtonIcon {
  @include resetDefaultAnchor;
  align-items: flex-start;
  color: $color-gray-dusk;
}

.hse-FileButtonIcon:hover,
.hse-FileButtonIcon:focus-visible {
  background: $color-gray-morn-4;
}

.hse-FileButtonIcon_size_large {
  height: $sizing-64;
}

.hse-FileButtonIcon_size_medium {
  height: $sizing-56;
}

.hse-FileButtonIcon_size_small {
  height: $sizing-32;
}

.hse-FileButtonBase_size_small.hse-FileButtonIcon {
  align-items: center;
}

.hse-FileButtonIcon__content,
.hse-FileButtonIcon__iconContainer {
  padding: $spacing-small-3x 0;
}

.hse-FileButtonIcon_size_small .hse-FileButtonIcon__content,
.hse-FileButtonIcon_size_small .hse-FileButtonIcon__iconContainer {
  padding: 0;
}

.hse-FileButtonIcon__content {
  flex-direction: column;
}

.hse-FileButtonIcon__fileName,
.hse-FileButtonIcon__fileSize {
  text-overflow: ellipsis;
  overflow: hidden;
}

.hse-FileButtonIcon__title {
  display: flex;
  @include text_caption_medium_bold;
}

.hse-FileButtonIcon_size_small .hse-FileButtonIcon__title {
  @include text_caption_small_bold;
}

.hse-FileButtonIcon_size_large .hse-FileButtonIcon__fileSize {
  margin-top: $spacing-small-3x / 2;
}

.hse-FileButtonIcon__fileSize {
  @include text_caption_small_regular;
  text-transform: uppercase;
}

.hse-FileButtonIcon_size_small .hse-FileButtonIcon__action {
  height: 100%;
}

.hse-FileButtonIcon__iconContainer {
  display: flex;
  height: 100%;
  margin-right: $spacing-small-3x;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
}

.hse-FileButtonIcon_size_large .hse-FileButtonIcon__iconContainer {
  width: $sizing-48;
}

.hse-FileButtonIcon_size_medium .hse-FileButtonIcon__iconContainer {
  width: $sizing-32;
}

.hse-FileButtonIcon_size_small .hse-FileButtonIcon__iconContainer {
  width: $sizing-24;
}
