@import '~@hse-design/tokens/lib/scss/breakpoints.map';
@import '../functions';

/**
  Файл содержит миксины медиа-запросов и классы для скрытия контента
 */

$breakpointPrefix: 'breakpoint-';

/// Приводит название брейкпоинта к названию в токенах
@function get-breakpoint-name($breakpoint) {
  @if $breakpoint and
    (str-index($breakpoint, '-')) and
    not(str-index($breakpoint, $breakpointPrefix))
  {
    $breakpoint: $breakpointPrefix + $breakpoint;
  }

  @return $breakpoint;
}

/// Возвращает индекс брейкпоинта в map по его названию
@function get-breakpoint-index($breakpoint) {
  $breakpointIndex: index(
    map-keys($breakpoints-map),
    get-breakpoint-name($breakpoint)
  );
  @return $breakpointIndex;
}

/// Получаем следующий брейкпоинт по его названию
@function get-next-breakpoint($breakpoint) {
  $breakpointIndex: get-breakpoint-index($breakpoint);
  $nextBreakpointIndex: $breakpointIndex + 1;
  $nextBreakpoint: null;

  @if $nextBreakpointIndex <= length($breakpoints-map) {
    $nextBreakpoint: nth(map-keys($breakpoints-map), $nextBreakpointIndex);
  }
  @return $nextBreakpoint;
}

/// Получаем предыдущий брейкпоинт по его названию
@function get-prev-breakpoint($breakpoint) {
  $breakpointIndex: get-breakpoint-index($breakpoint);
  $prevBreakpointIndex: $breakpointIndex - 1;
  $prevBreakpoint: null;

  @if $prevBreakpointIndex > 0 {
    $prevBreakpoint: nth(map-keys($breakpoints-map), $prevBreakpointIndex);
  }
  @return $prevBreakpoint;
}

@mixin hide-content {
  display: none !important;
}

/// Mixin добавляет медиа-запрос с min и max width брейкпоинта по его названию
/// в качестве max-width берется значение следующего брейкпоинта - 1px
@mixin respond($breakpoint) {
  $breakpoint: get-breakpoint-name($breakpoint);
  $breakpointIndex: index(map-keys($breakpoints-map), $breakpoint);

  @if $breakpointIndex {
    $from: map-get($breakpoints-map, $breakpoint);
    $nextBreakpointIndex: $breakpointIndex + 1;

    @if not($nextBreakpointIndex > length($breakpoints-map)) {
      $to: nth(map-values($breakpoints-map), $nextBreakpointIndex) - 1;
      @media (min-width: $from) and (max-width: $to) {
        @content;
      }
    } @else {
      @include respond-up($breakpoint) {
        @content;
      }
    }
  } @else {
    @error 'Unknown breakpoint #{$breakpoint}';
  }
}

/// Mixin добавляет медиа-запрос с min-width брейкпоинта по его названию
@mixin respond-up($breakpoint) {
  $breakpoint: get-breakpoint-name($breakpoint);

  @if map-has-key($breakpoints-map, $breakpoint) {
    $from: map-get($breakpoints-map, $breakpoint);

    @media (min-width: $from) {
      @content;
    }
  } @else {
    @error 'Unknown breakpoint #{$breakpoint}';
  }
}

/// Mixin добавляет медиа-запрос с max-width брейкпоинта по его названию
@mixin respond-down($breakpoint) {
  $breakpoint: get-breakpoint-name($breakpoint);
  $breakpointIndex: get-breakpoint-index($breakpoint);
  $nextBreakpointPx: 10000px;

  @if not($breakpointIndex) {
    @error 'Unknown breakpoint #{$breakpoint}';
  }

  @if $breakpointIndex and $breakpointIndex < length($breakpoints-map) {
    $nextBreakpointPx: nth(map-values($breakpoints-map), $breakpointIndex + 1);
  }

  @media (max-width: $nextBreakpointPx - 1) {
    @content;
  }
}
