@import '../../base';

@mixin BadgeLabel_size_small_x {
  @include text_label_small_2x_bold;
  padding: 3px 3px 2px 3px;
}

@mixin BadgeLabel_size_small {
  @include text_caption_x_small_bold;
  height: $sizing-40 / 2;
  padding: 1px 8px;
}

@mixin BadgeLabel_size_medium {
  @include text_caption_small_bold;
  height: $sizing-24;
  padding: 1px 12px;
}
