@import '../../base';

.hse-CarouselSlide {
  position: relative;
  height: 408px;
}

.hse-CarouselSlide__content {
  position: relative;
  margin: $spacing-large-2x 0 0;
  color: $color-light;
  @include respond('tablet-large') {
    margin: $spacing-large 0 0;
  }

  @include respond('tablet-small') {
    margin: $spacing-large 0 0;
  }

  @include respond-down('mobile-large') {
    margin: $spacing-medium 0 0;
  }
}

.hse-CarouselSlide__image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    content: '';
    background: $color-overlay-dark;
  }
}

.hse-CarouselSlide__title {
  z-index: 1;
  @include respond-up('tablet-small') {
    @include text_heading_h2;
  }
  @include respond-down('mobile-large') {
    @include text_heading_h3;
  }
}

.hse-CarouselSlide__description {
  @include text_paragraph_large;
  z-index: 1;
  margin-top: $spacing-small;

  @include respond-down('tablet-large') {
    margin-top: $spacing-small-x;
  }
}
