@import '../../base';
@import '../../baseText';

.hse-Switcher {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  vertical-align: middle;

  &:hover:not(.hse-Switcher_inactive) {
    .hse-SwitcherIcon {
      background: $color-gray-dusk-3;
    }
  }

  &:focus {
    outline: none;
  }
}

.hse-Switcher_checked {
  &:hover:not(.hse-Switcher_inactive) {
    .hse-SwitcherIcon {
      background-color: $color-brand-dark;
    }
  }
}

.hse-Switcher_inactive {
  cursor: default;

  .hse-Switcher__label {
    color: $color-gray-dusk-3;
  }
}

.hse-Switcher__input {
  @include hideDefaultInput;
}

.hse-Switcher__label {
  @include text_caption_medium_regular;
  display: inline-flex;
  margin-left: $spacing-small-3x;
  vertical-align: middle;
  color: $color-base-black;
}
