@import '../base';

.hse-Breadcrumbs {
  display: flex;
  align-items: center;
}

.hse-Breadcrumbs__item[class] {
  color: $color-gray-dusk;
  &.hse-Breadcrumbs__item_active {
    &, &:hover {
      color: $color-base-black;
    }
  }
}
a.hse-Link.hse-Breadcrumbs__item {
  margin-top: 2px;
}

.hse-Breadcrumbs__item:not(:last-child) {
  margin-right: $spacing-small-3x / 2;
}

.hse-Breadcrumbs__icon {
  color: $color-gray-dusk;
}
