@import '../../base.scss';
@import './mixins';

$assets-base-path: '../../' !default;

h1.hse-Heading,
h2.hse-Heading,
h3.hse-Heading,
h4.hse-Heading,
h5.hse-Heading,
h6.hse-Heading {
  margin: 0;
}

h1.hse-Heading_size_h1, .hse-Heading_size_h1 {
  @include text_heading_h1;

  &.hse-Heading_hasMargin {
    @include h1_hasMargin;
  }

  &.hse-Heading_adaptive {
    @include h1_adaptive;
  }
}

h2.hse-Heading_size_h2, .hse-Heading_size_h2 {
  @include text_heading_h2;

  &.hse-Heading_hasMargin {
    @include h2_hasMargin;
  }

  &.hse-Heading_adaptive {
    @include h2_adaptive;
  }
}

h3.hse-Heading_size_h3, .hse-Heading_size_h3 {
  @include text_heading_h3;

  &.hse-Heading_hasMargin {
    @include h3_hasMargin;
  }

  &.hse-Heading_adaptive {
    @include h3_adaptive;
  }
}

h4.hse-Heading_size_h4 {
  @include text_heading_h4;

  &.hse-Heading_hasMargin {
    @include h4_hasMargin;
  }
}

.hse-Paragraph {
  @include text_paragraph_large;
}

.hse-Paragraph_hasMargin {
  margin: $spacing-small 0;
}

.hse-Paragraph_small {
  @include text_paragraph_small;
}

.hse-Attention {
  @include text_paragraph_large;
  position: relative;
  padding: $spacing-small-2x $spacing-small;
  background-color: #f0f5ff;

  &::before {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: $spacing-small-3x / 2;
    content: '';
    border-radius: $radius-large;
    background-color: $color-brand;
  }
}

.hse-Attention_hasMargin {
  margin: $spacing-medium 0;
}

.hse-Blockquote {
  display: flex;
  @include respond-down('mobile-large') {
    flex-direction: column;
  }
}

.hse-Blockquote_hasMargin {
  margin: $spacing-large-x 0;
}

.hse-Blockquote_variant_normal {
  @include text_lead_small;

  .hse-Blockquote__right {
    @include respond-up('tablet-small') {
      margin-top: $spacing-small;
    }
  }

  .hse-Blockquote__content {
    margin-bottom: $spacing-small-x;
    color: $color-gray-dusk;
  }

  .hse-Blockquote__author {
    color: rgba($color-gray-dusk, $opacity-60);
  }
}

.hse-Blockquote_variant_rich {
  padding: $spacing-medium-x $spacing-medium;
  color: $color-base-black;
  border-radius: $radius-small;
  background-color: #f0f5ff;

  @include respond-down('mobile-large') {
    padding: $spacing-small $spacing-small-x;
  }

  .hse-Blockquote__left {
    @include respond-down('mobile-large') {
      margin-bottom: $spacing-small-x;
    }
  }

  .hse-Blockquote__right {
    @include respond-up('tablet-small') {
      margin-left: $spacing-medium;
    }
  }

  .hse-Blockquote__author {
    @include text_caption_x_large_bold;
    margin-bottom: $spacing-small-3x;
  }

  .hse-Blockquote__content {
    @include text_paragraph_large;
  }
}

.hse-Blockquote__commas {
  display: block;
  width: 59px;
  height: 52px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('#{$assets-base-path}assets/images/commas.svg');
  background-position: center center;
}

.hse-Ol {
  @include ol;
}

.hse-Ol_withShift {
  @include ol_withShift;
}

.hse-Ol_hasMargin {
  @include ol_hasMargin;
}

.hse-Ul {
  @include ul;
}

.hse-Ul_withShift {
  @include ul_withShift;
}

.hse-Ul_hasMargin {
  @include ul_hasMargin;
}

.hse-Li {
  @include li;
}

.hse-Img {
  display: flex;
}

.hse-Img_hasMargin {
  margin: $spacing-medium 0;
}

.hse-Img_description-variant_bottom {
  flex-flow: column nowrap;

  .hse-Img__description {
    @include text_paragraph_small;
    margin-top: $spacing-small-3x;
    color: rgba($color-base-black, $opacity-40);
  }
}

.hse-Img_description-variant_right {
  align-items: flex-end;
  flex-flow: row nowrap;

  .hse-Img__description {
    @include text_caption_medium_regular;
    margin-left: $spacing-small-3x + $spacing-medium;
    color: $color-gray-dusk;
  }
}

.hse-Img__img {
  max-width: 80%;
  min-height: 296px;
  max-height: 496px;
}

.hse-Columns {
  @include text_paragraph_large;
  columns: 2;
  column-gap: $spacing-large-x;
}
