@import '../base';

$hamburger-transition-time: $transition-quick;
$hamburger-bar-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
$hamburger-cross-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);

.hse-HeaderHamburger {
}

.hse-HeaderHamburger_isOpen {
}

.hse-HeaderHamburger__bars {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 18px;
  height: 12px;
}

.hse-HeaderHamburger__bar {
  display: block;
  width: 100%;
  height: 2px;
  transition: transform $hamburger-transition-time
    $hamburger-bar-timing-function $hamburger-transition-time;
  //background: $color-gray-dusk;
  background: currentColor;
  @media (prefers-reduced-motion: reduce) {
    transition: opacity $hamburger-transition-time
      $hamburger-cross-timing-function;
    opacity: 1;
  }
}

.hse-HeaderHamburger__bar:nth-of-type(1) {
  transform-origin: 0 50%;
}

.hse-HeaderHamburger__bar:nth-of-type(3) {
  transform-origin: 100% 0;
}

.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar:nth-of-type(1),
.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar:nth-of-type(2),
.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar:nth-of-type(3) {
  transition-delay: 0s;
  transform: scaleX(0);
  @media (prefers-reduced-motion: reduce) {
    transform: none;
    opacity: 0;
  }
}

.hse-HeaderHamburger__bar.hse-HeaderHamburger__bar_cross {
  position: absolute;
  top: calc(50% - 1px);
  transition: opacity $hamburger-cross-timing-function
      $hamburger-transition-time,
    transform $hamburger-cross-timing-function $hamburger-transition-time;
  transform-origin: 50% 50%;
  opacity: 0;
  @media (prefers-reduced-motion: reduce) {
    transition: opacity $hamburger-cross-timing-function
      $hamburger-transition-time;
  }
}

.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar_cross {
  transition-delay: $hamburger-transition-time;
  opacity: 1;
  @media (prefers-reduced-motion: reduce) {
    transition-delay: 0s;
  }
}

.hse-HeaderHamburger__bar_cross:nth-of-type(4) {
  transform: rotate(45deg) scaleX(0);
}

.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar_cross:nth-of-type(4) {
  transform: rotate(45deg) scaleX(1);
}

.hse-HeaderHamburger__bar_cross:nth-of-type(5) {
  transform: rotate(-45deg) scaleX(0);
}

.hse-HeaderHamburger_isOpen .hse-HeaderHamburger__bar_cross:nth-of-type(5) {
  transform: rotate(-45deg) scaleX(1);
}
