// Сетка
@import '~@hse-design/tokens/lib/scss/gridColumnsCount.map.scss';
@import '~@hse-design/tokens/lib/scss/gridColumnWidth.map.scss';
@import '~@hse-design/tokens/lib/scss/breakpoints.map.scss';
@import '~@hse-design/tokens/lib/scss/gridGutters.map.scss';
@import '~@hse-design/tokens/lib/scss/gridPadding.map.scss';
@import '~@hse-design/tokens/lib/scss/sidebarWidth.map.scss';
@import 'functions';
@import 'mixins/index';

///  Mixin для колонки грида
///  Если не указано название брейкпоинта, то css правила добавляются без медиа-запроса
///
/// @example scss
/// .my-col {
///   @include grid-column(12, 'breakpoint-desktop-small'); // или desktop-small
/// }
/// @param {Number} $col - Количество колонок
/// @param {String} $breakpointName - Название брейкпоинта
///

$grid-sidebar-fixed-name: 'fixed';
$grid-sidebar-dynamic-name: 'dynamic';

@mixin grid-column($col: 1, $breakpointName: '') {
  $minWidth: map-get($breakpoints-map, $breakpointName);
  @if $breakpointName and not (str-index($breakpointName, 'breakpoint-')) {
    $breakpointName: 'breakpoint-' + $breakpointName;
  }

  @if not $minWidth {
    grid-column: span #{$col};
  } @else {
    @include respond-up($breakpointName) {
      grid-column: span #{$col};
    }
  }
}

//  Класс сетки
//  Выставляет нужное количество колонок, их ширину, gutter на разных брейкпоинтах
.grid {
  display: grid;

  @each $breakpointName, $resolution in $breakpoints-map {
    @include respond-up($breakpointName) {
      $name: str-replace($breakpointName, 'breakpoint-', '');
      $columnsCount: map-get($gridColumnsCount-map, 'columns-' + $name);
      $columnWidth: map-get($gridColumnWidth-map, 'column-width-' + $name);
      $sidebarWidthOpen: map-get(
        $sidebarWidth-map,
        'sidebar-width-' + $name + '-' + $grid-sidebar-fixed-name
      );
      $sidebarWidthClosed: map-get(
        $sidebarWidth-map,
        'sidebar-width-' + $name + '-' + $grid-sidebar-dynamic-name
      );
      $gutter: map-get($gridGutters-map, 'gutter-' + $name);
      $gutterWithSidebar: map-get(
        $gridGutters-map,
        'gutter-' + $name + '-with-sidebar'
      );
      $paddingName: 'padding-' + $name;
      $padding: map-get($gridPadding-map, $paddingName);
      $paddingRightOpen: map-get(
        $gridPadding-map,
        $paddingName + '-with-sidebar-' + $grid-sidebar-fixed-name
      );
      $paddingRightClosed: map-get(
        $gridPadding-map,
        $paddingName + '-with-sidebar-' + $grid-sidebar-dynamic-name
      );

      justify-content: center;
      grid-template-columns: repeat($columnsCount, $columnWidth);
      grid-gap: 0 $gutter;

      // Добавляет боковые отступы для маленьких разрешений
      @if $breakpointName ==
        'breakpoint-mobile-small' or
        $breakpointName ==
        'breakpoint-mobile-large'
      {
        padding-right: $padding;
        padding-left: $padding;
      }

      // Для сетки с сайдбаром нужны другие настройки грида
      @if $gutterWithSidebar {
        &.grid_sidebar_#{$grid-sidebar-fixed-name},
        &.grid_sidebar_#{$grid-sidebar-dynamic-name} {
          justify-content: start;
          grid-gap: 0 $gutterWithSidebar;
        }
        &.grid_sidebar_#{$grid-sidebar-fixed-name} {
          padding-right: $paddingRightOpen;
          grid-template-columns: (
              $sidebarWidthOpen + $paddingRightOpen - $gutterWithSidebar
            ) repeat($columnsCount, 1fr);
          .col-sidebar {
            margin-right: $paddingRightOpen - $gutterWithSidebar;
          }
        }
        &.grid_sidebar_#{$grid-sidebar-dynamic-name} {
          padding-right: $paddingRightClosed;
          grid-template-columns: (
              $sidebarWidthClosed + $paddingRightClosed - $gutterWithSidebar
            ) repeat($columnsCount, 1fr);
          .col-sidebar {
            margin-right: $paddingRightClosed - $gutterWithSidebar;
          }
        }
      }
    }

    // Добавляем колонки для разных разрешений
    $colName: str-replace($breakpointName, 'breakpoint-', '');
    $columnsCount: map-get($gridColumnsCount-map, 'columns-' + $colName);

    @for $i from 1 through $columnsCount {
      .col-#{$colName}-#{$i} {
        @include grid-column($i, $breakpointName);
      }
    }
  }
}

// Скрываем сайдбар и показываем его только для тех разрешений, которые это поддерживают
.grid .col-sidebar {
  display: none;
}

.grid.grid_sidebar_#{$grid-sidebar-fixed-name} .col-sidebar,
.grid.grid_sidebar_#{$grid-sidebar-dynamic-name} .col-sidebar {
  @each $name, $value in $sidebarWidth-map {
    // получаем название breakpoint из sidebarWidth
    $bpName: str-replace($name, 'sidebar-width', 'breakpoint');
    $bpName: str-replace($bpName, '-' + $grid-sidebar-fixed-name, '');
    $bpName: str-replace($bpName, '-' + $grid-sidebar-dynamic-name, '');

    @if map-get($breakpoints-map, $bpName) {
      @include respond-up($bpName) {
        display: block;
      }
    }
  }
}
