@import '~@hse-design/core/src/base';

.hse-NewItemButton {
  @include text_caption_small_bold;
  height: 72px;
  padding: $spacing-small;
  cursor: pointer;
  transition: all $transition-quick ease-out;
  color: $color-gray-dusk;
  border: 1px dashed $color-gray-dusk-3;
  border-radius: $radius-small;
  outline: none;
  background-color: transparent;

  &:not(.hse-NewItemButton_disabled):hover,
  &:not(.hse-NewItemButton_disabled):focus-visible {
    color: $color-brand;
    border: 1px dashed $color-brand;
  }
}
.hse-NewItemButton__icon {
  margin-right: $spacing-small-2x;
  color: inherit;
}

.hse-NewItemButton_disabled {
  cursor: no-drop;
  color: $color-gray-dusk-3;
  border: 1px dashed $color-gray-morn;
}

.hse-NewItemButton__inner {
  display: flex;
  align-items: center;
}
