@import '../base';

.hse-TapBar {
  position: fixed;
  z-index: $z-index-tapbar;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  height: 50px;
  background: $color-background-white;
  box-shadow: $shadow-hover;
}

.hse-TapBar_active .hse-TapBar__list {
  transform: translateY(0);
}

.hse-TapBar__list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  transition: transform $transition-quick linear;
  transform: translateY(100%);
}

.hse-TapBar .hse-TapBarButton {
  padding: $spacing-small-3x;

  @include respond-down('mobile-small') {
    padding: $spacing-small-3x / 2;
  }
}
