@import '../../base.scss';
@import './mixins';

.hse-Input {
  @include Input;
}

.hse-Input_size_small {
  @include Input_size_small;
}

.hse-Input_size_medium {
  @include Input_size_medium;
}

.hse-Input_size_large {
  @include Input_size_large;
}

.hse-Input_focus {
  @include Input_focus;
}

.hse-Input_error {
  @include Input_error;
}

.hse-Input_disabled {
  @include Input_disabled;
}

.hse-Input_fullWidth {
  @include Input_fullWidth;
}

.hse-Input_disabled .hse-Input__input {
  @include Input__input_disabled;
}

.hse-Input_disabled .hse-Input__input::placeholder {
  @include Input__placeholder_disabled;
}

.hse-Input__input {
  @include Input__input;
}

.hse-Input__input::placeholder {
  @include Input__placeholder;
}

.hse-Input__icon {
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  min-width: $sizing-16;
  max-width: $sizing-40;
  max-height: 100%;
}

.hse-Input__icon_left {
  justify-content: flex-start;
  margin-right: $spacing-small-3x;
}

.hse-Input__icon_right {
  justify-content: flex-end;
  margin-left: $spacing-small-3x;
}
