@import '../../base';

/// Используем нестадартные отступы (не кратные 4) по следующим причинам:
/// Текст внутри контрола может быть расположен в нескольких строках
/// Высота контрола с одной строкой текста должна быть равна высоте кнопке
$horizontal-spacing: 13px;
/// Стили, специфичные для размера
$sizes: (
  'small': (
    padding: 5px $horizontal-spacing
  ),
  'medium': (
    padding: 9px $horizontal-spacing
  ),
  'large': (
    padding: $horizontal-spacing
  )
);
/// Ширина обводки главного элемента при фокусе
$outline-width: 2px;
/// Ширина обводки элемента
$segment-border-width: 1px;

.hse-SegmentedControl {
  position: relative;
  display: inline-flex;
  transition: border-color ease $transition-quick;
  vertical-align: middle;
  border: $outline-width solid transparent;
  border-radius: $radius-small + $outline-width;
  outline: none;

  &,
  & > * {
    box-sizing: content-box;
    user-select: none;
    background: transparent;
  }
}

.hse-SegmentedControl:focus-visible,
.hse-SegmentedControl_focus {
  border-color: $color-brand-4;
}

@each $sizeName, $props in $sizes {
  .hse-SegmentedControl_size_#{$sizeName} {
    .hse-SegmentedControl__item {
      @each $propName, $propValue in $props {
        #{$propName}: #{$propValue};
      }
    }
  }
}

.hse-SegmentedControl__item {
  @include text_caption_small_bold;
  @include resetDefaultInput;
  position: relative;
  display: block;
  cursor: pointer;
  transition: all ease $transition-quick;
  text-align: center;
  color: $color-brand;
  border: $segment-border-width solid $color-gray-morn;

  &::after {
    position: absolute;
    top: -1 * $segment-border-width;
    right: -1 * $segment-border-width;
    bottom: -1 * $segment-border-width;
    left: -1 * $segment-border-width;
    content: '';
    transition: opacity ease $transition-quick;
    opacity: 0;
    border: $segment-border-width solid $color-brand;
  }
}

.hse-SegmentedControl__item:first-child,
.hse-SegmentedControl__item:first-child::after {
  border-radius: $radius-small 0 0 $radius-small;
}

.hse-SegmentedControl__item:last-child,
.hse-SegmentedControl__item:last-child::after {
  border-radius: 0 $radius-small $radius-small 0;
}

.hse-SegmentedControl__item:not(:first-child) {
  margin-left: -1 * $segment-border-width;
}

.hse-SegmentedControl__item:focus,
.hse-SegmentedControl__item_focus,
.hse-SegmentedControl__item:focus-visible,
.hse-SegmentedControl__item:hover,
.hse-SegmentedControl__item_hover {
  background: $color-brand-5;
}

.hse-SegmentedControl__item:active,
.hse-SegmentedControl__item_active {
  z-index: 1;
  background: $color-brand-5;

  &::after {
    opacity: 1;
  }
}

.hse-SegmentedControl__item_inactive,
.hse-SegmentedControl__item_disabled,
.hse-SegmentedControl__item:disabled {
  user-select: none;
  pointer-events: none;
  color: $color-gray-dusk;
  &::after {
    content: none;
  }
}
