/** В этом файле определяются основные шрифты дизайн-системы */

@import '~@hse-design/tokens/lib/scss/fontFamily.scss';
$fonts-path: './assets/fonts' !default;

@font-face {
  font-family: $font-family-proxima-nova;
  src: url('#{$fonts-path}/proxima-nova/ProximaNova-Bold.woff2') format('woff2'),
    url('#{$fonts-path}/proxima-nova/ProximaNova-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: $font-family-proxima-nova;
  src: url('#{$fonts-path}/proxima-nova/ProximaNova-Semibold.woff2')
      format('woff2'),
    url('#{$fonts-path}/proxima-nova/ProximaNova-Semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

