@import '../base';

.hse-FileInput {
  position: relative;
  display: inline-block;
  min-width: 207px;
  color: $color-brand;
}

.hse-FileInput:not(.hse-FileInput_dropzone_active)
  .hse-FileInput__content:hover {
  background: $color-brand-5;
}

.hse-FileInput:hover .hse-FileInput__hotspot {
  opacity: 1;
}

/// Modifiers

.hse-FileInput_size_large .hse-FileInput__content,
.hse-FileInput_size_large .hse-FileInput__dropzone {
  height: $sizing-48;
}

.hse-FileInput_size_medium .hse-FileInput__content,
.hse-FileInput_size_medium .hse-FileInput__dropzone {
  height: $sizing-40;
}

.hse-FileInput_size_small .hse-FileInput__content,
.hse-FileInput_size_small .hse-FileInput__dropzone {
  height: $sizing-32;
}

.hse-FileInput_error .hse-FileInput__hint {
  color: $color-system-error;
}

.hse-FileInput_error .hse-FileInput__content {
  background: $color-system-error-light;
}

/// Elements

.hse-FileInput__inputLabel,
.hse-FileInput__dropzone,
.hse-FileInput__button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

//.hse-FileInput_hasDropzone .hse-FileInput__input,
.hse-FileInput_hasDropzone .hse-FileInput__inputLabel,
.hse-FileInput_hasDropzone .hse-FileInput__dropzone,
.hse-FileInput_hasDropzone .hse-FileInput__button {
  right: -1 * $spacing-small;
  left: -1 * $spacing-small;
}

/// inputLabel (<label />) нужен для того, чтобы вся зона компонента FileInput была кликабельной
/// Используя один инпут <input type="file" />, нельзя скрыть дефолтную кнопку Choose File, даже выставляя font-size: 0.
/// При наведении на кнопку Choose File курсор становится дефолтным, мы этого не хотим
.hse-FileInput__inputLabel {
  z-index: 1;
  cursor: pointer;
  opacity: 0;
}

.hse-FileInput_hasDropzone .hse-FileInput__inputLabel,
.hse-FileInput_hasDropzone .hse-FileInput__button {
  width: calc(100% + #{2 * $spacing-small});
}

.hse-FileInput__input {
  position: absolute;
  z-index: -1;
  width: 0.1px;
  height: 0.1px;
  margin: 0;
  padding: 0;
  cursor: pointer;
  opacity: 0;
  border: 0;

  &::-webkit-file-upload-button {
    visibility: hidden;
  }
}

.hse-FileInput__content {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  height: 100%;
  transition: background-color $transition-quick ease-out;
}

.hse-FileInput__content_focused {
  @include focus-shadow;
  border-radius: $radius-small;
}

.hse-FileInput__fileIcon {
  flex: 0 0 auto;
  margin-right: $spacing-small-3x;
  color: currentColor;
}

.hse-FileInput__info {
  position: relative;
  display: flex;
  overflow: hidden;
  width: 100%;
}

.hse-FileInput__fileName {
  @include text_caption_small_bold;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.hse-FileInput__fileExtension {
  @include text_caption_small_bold;
  flex-shrink: 0;
}

.hse-FileInput__progressBar {
}

.hse-FileInput__hint {
  @include text_caption_x_small_regular;
  color: $color-gray-dusk;
}

.hse-FileInput__hotspot {
  display: flex;
  align-items: center;
  flex: 0 0 auto;
  min-width: $sizing-40;
  max-width: $sizing-64;
  height: 100%;
  margin-left: $spacing-small-3x;
  transition: opacity $transition-quick ease-out;
  opacity: 0;
}

.hse-FileInput__hotspot_twoIcons {
  padding: 0 10px;
}

.hse-FileInput__hotspot_twoIcons .hse-FileInput__hotspotIcon {
  width: 20px;
  margin-left: $spacing-small-3x / 2;
}

.hse-FileInput__hotspotIcon {
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  width: $sizing-40;
  height: 100%;
}

.hse-FileInput__hotspotAction {
}

.hse-FileInput__dropzone {
  transition: opacity $transition-quick ease-out,
    background-color $transition-quick ease-out,
    border-color $transition-quick ease-out;
  pointer-events: none;
  border: 1px dashed transparent;
  border-radius: $radius-small;
}

.hse-FileInput__bubbleRoot {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.hse-FileInput__bubbleRoot .hse-Button__bubble {
  background: $color-brand-5;
}

.hse-FileInput__dropzone_active {
  border-color: currentColor;
}

.hse-FileInput__dropzone_hovered {
  background: $color-brand-5;
}
