@import '../base';

.hse-TapBarButton {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: $sizing-24 * 3 + $spacing-small-3x * 2;
  height: 100%;
  padding: 0;
  cursor: pointer;
  user-select: none;
  transition: color 0.2s;
  text-align: center;
  color: $color-gray-dusk;
  border: none;
  outline: none;
  line-height: 1;

  @include respond-down('mobile-small') {
    width: $sizing-24 * 3 + $spacing-small-3x;
  }
}

.hse-TapBarButton_active {
  color: $color-brand;
}

.hse-TapBarButton_disabled {
  cursor: not-allowed;
  color: $color-gray-dusk-4;
}

.hse-TapBarButton__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.hse-TapBarButton__icon svg {
  color: currentColor;
}

.hse-TapBarButton__label {
  white-space: nowrap;
  font-family: $font-family-proxima-nova;
  font-size: 10px;
  font-weight: $font-weight-600;
  line-height: 1.4;
}

.hse-TapBarButton__bubbleRoot {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.hse-TapBarButton__bubbleRoot .hse-Button__bubble {
  transform: translate(-50%, -50%) scale(0);
  background: rgba($color-brand, $opacity-10);
}

.hse-TapBarButton__bubbleRoot .hse-Button__bubble_active {
  transform: translate(-50%, -59%) scale(0.4);
  opacity: $opacity-40;
}

.hse-TapBarButton__bubbleRoot .hse-Button__bubble_fading {
  transform: translate(-50%, -50%) scale(1);
  opacity: 0;
}
