/** В данном файле определяются миксины иконки чекбокса/радио */

@mixin CheckboxIcon_hover {
  border-color: $color-brand;
}

@mixin CheckboxIcon_focus {
  box-shadow: 0 0 0 2px $color-brand-4;
}

@mixin CheckboxIcon_inactive {
  color: $color-gray-dusk-4;
  border-color: transparent;
  background-color: $color-gray-morn-2;
  box-shadow: none;
}

@mixin CheckboxIcon_checked_hover {
  border-color: transparent;
  background-color: $color-brand-dark;
}

@mixin CheckboxIcon_checked_focus {
  @include CheckboxIcon_focus;
  border-color: transparent;
  background-color: $color-brand-dark;
}

@mixin CheckboxIcon_radio_checked_hover {
  border: 6px solid $color-brand-dark;
  background-color: transparent;
}

@mixin CheckboxIcon_radio_checked_focus {
  @include CheckboxIcon_focus;
  border: 6px solid $color-brand-dark;
  background-color: transparent;
}

@mixin CheckboxIcon_radio_checked_inactive {
  border: 6px solid $color-gray-morn;
  background-color: transparent;
}
