@import '../../base';
@import '../../baseText';
@import './vars';
@import './mixins';

.hse-Button {
  @include Button;
}

.hse-Button_disabled,
.hse-Button_inactive {
  @include Button_disabled;
}

.hse-Button_focused,
.hse-Button:focus-visible {
  @include Button_focus;
}

.hse-Button,
.hse-Button::before,
.hse-Button__content {
  transition: color $transition-quick ease-out,
    background-color $transition-quick ease-out,
    border-color $transition-quick ease-out,
    box-shadow $transition-quick ease-out;
}

/// Padding with left / right slots

// Есть слоты слева или справа
.hse-Button_hasSideContent {
  padding: 0 $spacing-small-x;
}

// Справа от текста есть слот
.hse-Button_hasRight:not(.hse-Button_hasLeft) .hse-Button__text {
  padding: 0 0 0 $spacing-small-3x;
}

// Слева от текста есть слот
.hse-Button_hasLeft:not(.hse-Button_hasRight) .hse-Button__text {
  padding: 0 $spacing-small-3x 0 0;
}

// Есть слоты слева или справа
.hse-Button_size_small.hse-Button_hasSideContent {
  padding: 0 $spacing-small-2x;
}

// Справа от текста есть слот
.hse-Button_size_small.hse-Button_hasRight:not(.hse-Button_hasLeft)
  .hse-Button__text {
  padding: 0 0 0 $spacing-small-3x/2;
}

// Слева от текста есть слот
.hse-Button_size_small.hse-Button_hasLeft:not(.hse-Button_hasRight)
  .hse-Button__text {
  padding: 0 $spacing-small-3x/2 0 0;
}

// Элементы со слотами
.hse-Button__contentLeft,
.hse-Button__contentRight {
  display: flex;
  align-items: center;
}

.hse-Button__contentLeft {
  margin-right: $spacing-small-3x;
}

.hse-Button__contentRight {
  margin-left: $spacing-small-3x;
}

// Бабл
.hse-Button__bubbleRoot {
  position: absolute;
  top: -1 * $button-border-width;
  right: -1 * $button-border-width;
  bottom: -1 * $button-border-width;
  left: -1 * $button-border-width;
  overflow: hidden;
}

.hse-Button__bubble {
  position: absolute;
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 100%;
  padding-top: 100%;
  transition: $transition-quick transform ease-out,
    $transition-quick opacity ease-out;
  transform: translate(-50%, -50%) scale(0);
  pointer-events: none;
  opacity: $opacity-40;
  border-radius: 50%;
  background: $color-base-white;
}

.hse-Button__bubble_active {
  transform: translate(-50%, -50%) scale(0.7);
  opacity: $opacity-40;
}

.hse-Button__bubble_fading {
  transform: translate(-50%, -50%) scale(1);
  opacity: 0;
}

.hse-Button__content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  text-align: center;
}

.hse-Button_custom-color {
  color: currentColor;
}

.hse-Button_size_small {
  @include Button_size_small;
}

.hse-Button_size_medium {
  @include Button_size_medium;
}

.hse-Button_size_large {
  @include Button_size_large;
}

.hse-Button_variant_primary {
  @include Button_variant_primary;
}

.hse-Button_variant_primary.hse-Button_custom-color {
  background-color: currentColor;
}

.hse-Button_variant_primary.hse-Button_focused:not(.hse-Button_hasSpinner):not(.hse-Button_inactive):not(.hse-Button_disabled) {
  &::before {
    background-color: rgba($color-element-overlay-dark, $opacity-60);
  }
}

.hse-Button_variant_primary.hse-Button_disabled {
  @include Button_variant_primary_disabled;
}

.hse-Button_variant_primary.hse-Button_inactive {
  pointer-events: none;
  border: 1px solid $color-gray-dusk-5;
  background-color: $color-gray-morn-3;
  box-shadow: none;

  &,
  .hse-Button__content {
    color: $color-gray-dusk-4;
  }
}

.hse-Button_variant_secondary {
  @include Button_variant_secondary;
}

.hse-Button_variant_secondary.hse-Button_focused:not(.hse-Button_hasSpinner):not(.hse-Button_inactive):not(.hse-Button_disabled) {
  color: $color-brand;
  border-color: $color-brand;

  &.hse-Button_custom-color {
    border-color: currentColor;

    &,
    .hse-Button__content {
      color: currentColor;
    }
  }
}

.hse-Button_variant_secondary.hse-Button_disabled {
  @include Button_variant_secondary_disabled;
}

.hse-Button_variant_secondary.hse-Button_inactive {
  background-color: $color-gray-morn;
  box-shadow: none;

  &::before {
    background-color: rgba($color-element-overlay-light, $opacity-60);
  }

  &,
  .hse-Button__content {
    color: $color-gray-dusk-4;
  }
}

.hse-Button_variant_tertiary {
  @include Button_variant_tertiary;
}

.hse-Button_variant_tertiary.hse-Button_selected {
  @include Button_variant_tertiary_selected;
}

.hse-Button_variant_tertiary.hse-Button_selected.hse-Button_focused {
  @include Button_variant_tertiary_selected_focused;
}

.hse-Button_variant_tertiary.hse-Button_focused:not(.hse-Button_hasSpinner):not(.hse-Button_selected):not(.hse-Button_inactive):not(.hse-Button_disabled) {
  @include Button_variant_tertiary_focused;
}

.hse-Button_variant_tertiary.hse-Button_disabled {
  @include Button_variant_tertiary_disabled;
}

.hse-Button_variant_tertiary.hse-Button_inactive {
  @include Button_variant_tertiary_inactive;
}

// Spinner

.hse-Button_spinner_done .hse-Spinner__overlay {
  display: none;
}

.hse-Button.hse-Button_hasSpinner {
  --checkmark-fill-box-shadow: #{$color-gray-morn-3};
  background: $color-gray-morn-3;

  &::before {
    background: transparent;
  }

  .hse-Button__content {
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba($color-gray-dusk, $opacity-40);
  }
}

.hse-Button__icon.hse-Icon {
  color: currentColor;
}

// tertiaryInvert variant

.hse-Button.hse-Button_variant_tertiaryInvert {
  color: $color-base-white;
}

.hse-Button.hse-Button_variant_tertiaryInvert .hse-Button__content {
  color: currentColor;
}

.hse-Button.hse-Button_variant_tertiaryInvert:hover::before {
  background: rgba($color-element-overlay-light, $opacity-20);
}

.hse-Button.hse-Button_variant_tertiaryInvert.hse-Button_disabled
  .hse-Button__content,
.hse-Button.hse-Button_variant_tertiaryInvert.hse-Button_inactive
  .hse-Button__content {
  color: rgba($color-base-white, $opacity-60);
}
