@import '../base';

.hse-Alert {
  padding: $spacing-small-x $spacing-large-2x + $spacing-small-x;

  @include respond('desktop-small') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x;
  }

  @include respond('tablet-large') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x
      $spacing-small;
  }

  @include respond('tablet-small') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x
      $spacing-small;
  }

  @include respond('mobile-large') {
    padding: $spacing-small-x $spacing-large $spacing-small $spacing-small-x;
  }

  @include respond('mobile-small') {
    padding: $spacing-small-x $spacing-small + $spacing-small-2x $spacing-small
      $spacing-small-x;
  }
}

.hse-Alert.hse-BaseAlert_hasDescription {
  padding: $spacing-small-x $spacing-large-4x - $spacing-small-2x $spacing-small;

  @include respond('desktop-small') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x;
  }
  @include respond('tablet-large') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x;
  }
  @include respond('tablet-small') {
    padding: $spacing-small-x $spacing-large-2x + $spacing-small-x;
  }
  @include respond('mobile-large') {
    padding: $spacing-small-x $spacing-medium + $spacing-small-3x
      $spacing-small-x $spacing-small-x;
  }
  @include respond('mobile-small') {
    padding: $spacing-small-x $spacing-large - $spacing-small-2x / 2
      $spacing-small-x $spacing-small-x;
  }
}

.hse-Alert.hse-BaseAlert_hasDescription .hse-BaseAlert__wrapper {
}

.hse-Alert:not(.hse-BaseAlert_hasDescription) .hse-BaseAlert__wrapper {
  justify-content: center;
  @include respond-down('tablet-large') {
    justify-content: flex-start;
  }
}

.hse-Alert:not(.hse-BaseAlert_hasDescription) .hse-BaseAlert__content {
  align-items: baseline;
  width: initial;
  max-width: 1216px;

  @include respond-down('tablet-large') {
    align-items: flex-start;
  }
}

.hse-Alert .hse-BaseAlert__closeButton {
  position: absolute;
  top: $spacing-small-x;
  right: $spacing-large + $spacing-small-2x / 2;
  @include respond('desktop-small') {
    right: $spacing-large;
  }
  @include respond('tablet-large') {
    right: $spacing-large;
  }
  @include respond('tablet-small') {
    right: $spacing-large;
  }
  @include respond-down('mobile-large') {
    right: $spacing-small-x;
  }
}

.hse-Alert .hse-BaseAlert__icon {
  @include respond-down('mobile-large') {
    flex: 0 0 $sizing-16;
  }
}
