@import '../../base';
@import './vars';

@mixin Collapse {
  display: block;
  width: 100%;
}

@mixin Collapse__header {
  display: block;
  width: 100%;
  padding: $spacing-small-x 0;
  cursor: pointer;
  user-select: none;
  text-align: left;
  border-top: $collapse-border;
  border-right: none;
  border-bottom: $collapse-border;
  border-left: none;
  outline: none;
}

@mixin Collapse__titleBlock {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  border-radius: $radius-small;
}

@mixin Collapse__titleBlock_focus_visible {
  @include focus-shadow;
}

@mixin Collapse__title {
  @include text-paragraph-large;
  transition: color $transition-quick ease-out;
  color: $color-base-black;
}

@mixin Collapse__toggleIcon {
  flex: 0 0 $sizing-24;
  margin-left: $spacing-small-x;
}

@mixin Collapse__contentWrapper {
  display: block;
  overflow: hidden;
  width: 100%;
  height: 0;
}

@mixin Collapse__content {
  @include text_paragraph_large;
  display: block;
  width: 100%;
  padding: 0 0 $spacing-medium 0;
  transition: opacity $transition-quick ease;
  opacity: 0;
  color: $color-dusk;
}
