// @define RangeSlider

@import '../../base';
@import './vars';
@import './mixins';

.hse-RangeSlider {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  height: 40px;
  padding: $spacing-small-3x 0;
  user-select: none;

  &::before {
    position: absolute;
    top: -2px;
    right: -2px;
    bottom: -2px;
    left: -2px;
    display: block;
    margin: -2px;
    content: '';
    pointer-events: none;
    border: 2px solid transparent;
    border-radius: $radius-small;
  }
}

.hse-RangeSlider:focus {
  outline: none;
}

.hse-RangeSlider:focus-visible {
  &::before {
    border: 2px solid $color-brand-4;
  }
}

.hse-RangeSlider__controls {
  position: absolute;
  top: 0;
  right: $range-slider-handle-size/2;
  bottom: 0;
  left: $range-slider-handle-size/2;
}

.hse-RangeSlider__control {
  position: absolute;
  z-index: 1;
  top: 50%;
  display: flex;
  align-items: center;
  flex-flow: column nowrap;
}

.hse-RangeSlider__control_disabled {
  z-index: 0;
}

.hse-RangeSlider__control:hover,
.hse-RangeSlider__control_active {
  &:not(.hse-RangeSlider__control_disabled) {
    z-index: 2;

    .hse-RangeSlider__controlText {
      transform: scale(1);
      opacity: 1;
    }
  }
}

.hse-RangeSlider__control_focused {
  z-index: 2;
}

.hse-RangeSlider__emptyTrack {
  position: absolute;
  top: 2 * $spacing-small-3x;
  right: 0;
  left: 0;
  height: $range-slider-track-height;
  border-radius: $range-slider-track-radius;
  background-color: $color-gray-morn-4;
}

.hse-RangeSlider__trackFill {
  position: absolute;
  top: calc(50% - #{$range-slider-track-height / 2});
  height: $range-slider-track-height;
  border-radius: $range-slider-track-radius;
  background-color: $color-brand-4;
}

.hse-RangeSlider__controlDot {
  position: absolute;
  top: -$range-slider-handle-size/2;
  left: -$range-slider-handle-size/2;
  width: $range-slider-handle-size;
  height: $range-slider-handle-size;
  cursor: pointer;
  border: 1px solid $color-brand;
  border-radius: 50%;
  background-color: $color-base-white;
}

.hse-RangeSlider__controlDot:hover,
.hse-RangeSlider__controlDot_active {
  background-color: $color-brand;
}

.hse-RangeSlider__controlDot_focused {
  border: 2px solid $color-brand-4;
  background-color: $color-brand;
}

.hse-RangeSlider__controlDot_disabled,
.hse-RangeSlider__controlDot_disabled:hover {
  cursor: not-allowed;
  border: 1px solid $color-gray-morn-2;
  background: $color-gray-morn-3;
}

.hse-RangeSlider__controlText {
  @include text_caption_small_regular;
  position: absolute;
  top: -70px;
  padding: 12px;
  transition: transform 0.3s;
  transform: scale(0.8);
  white-space: nowrap;
  pointer-events: none;
  opacity: 0;
  color: $color-base-black;
  border-radius: 12px;
  background: $color-brand-5;

  &::after {
    position: absolute;
    bottom: -3px;
    left: calc(50% - 3px);
    width: 8px;
    height: 8px;
    content: '';
    transform: rotate(45deg);
    background: $color-brand-5;
  }
}
