@import '../base';

$color-hover: mix(
  $color-text-gray-dusk-1,
  $color-element-overlay-dark,
  $weight: 60%
);

.hse-ClickableIcon {
  display: flex;
  align-items: center;
  padding: 0;
  border-radius: $radius-small;
}

.hse-ClickableIcon,
.hse-ClickableIcon:hover,
.hse-ClickableIcon:focus {
  border: none;
  outline: none;
}

.hse-ClickableIcon__icon {
  color: var(--clickable-icon-color-default, $color-gray-dusk-1);
}

.hse-ClickableIcon__icon_disabled {
  color: var(--clickable-icon-color-disabled, $color-gray-dusk-4);
}

.hse-ClickableIcon:not(:disabled) {
  cursor: pointer;

  &:hover,
  &:focus-visible,
  &:not(:focus-visible).hse-ClickableIcon_controlledFocus.hse-ClickableIcon_focused {
    .hse-ClickableIcon__icon {
      color: var(--clickable-icon-color-hover, $color-hover);
    }
  }

  &:active {
    .hse-ClickableIcon__icon {
      color: var(--clickable-icon-color-active, $color-brand-1);
    }
  }
}

.hse-ClickableIcon__icon_hovered,
.hse-ClickableIcon__icon_focused {
  color: var(--clickable-icon-color-hover, $color-hover);
}

.hse-ClickableIcon_focused,
.hse-ClickableIcon:not(:disabled):not(.hse-ClickableIcon_controlledFocus):focus-visible,
.hse-ClickableIcon.hse-ClickableIcon_controlledFocus.hse-ClickableIcon__icon_focused {
  @include focus-shadow;
}
