@import '../../base';
@import './vars';
@import './mixins';

.hse-Collapse {
  @include Collapse;
}

/// Elements

.hse-Collapse__header {
  @include Collapse__header;
}

.hse-Collapse__titleBlock {
  @include Collapse__titleBlock;
}

.hse-Collapse__header:focus-visible .hse-Collapse__titleBlock {
  @include Collapse__titleBlock_focus_visible;
}

.hse-Collapse__header:focus-visible .hse-Collapse__title,
.hse-Collapse__header:hover .hse-Collapse__title,
.hse-Collapse_isOpen .hse-Collapse__title {
  color: $color-brand;
}

.hse-Collapse__title {
  @include Collapse__title;
}

.hse-Collapse__toggleIcon,
.hse-Collapse__toggleIcon.hse-Icon {
  color: $color-dusk;
}

.hse-Collapse__toggleIcon {
  @include Collapse__toggleIcon;
}

.hse-Collapse__toggleIcon.hse-Icon {
  transition: transform $transition-quick linear;
  transform: rotate(0deg);
  transform-origin: center center;
}

.hse-Collapse__contentWrapper {
  @include Collapse__contentWrapper;
}

.hse-Collapse__content {
  @include Collapse__content;
}

/// Modifiers

/// hasMargin

.hse-Collapse_hasMargin {
  margin: $spacing-medium 0;
}

/// Color
.hse-Collapse_color_invert .hse-Collapse__header {
  border-top: $collapse-invert-border;
  border-bottom: $collapse-invert-border;
}

.hse-Collapse_color_invert
  .hse-Collapse__header:focus-visible
  .hse-Collapse__title,
.hse-Collapse_color_invert .hse-Collapse__header:hover .hse-Collapse__title,
.hse-Collapse_color_invert.hse-Collapse_isOpen .hse-Collapse__title {
  color: $color-brand-4;
}

.hse-Collapse_color_invert .hse-Collapse__title {
  color: $color-text-base-white;
}

.hse-Collapse_color_invert .hse-Collapse__content {
  color: $color-element-overlay-light-op60;
}

/// isOpen state

.hse-Collapse_isOpen .hse-Collapse__header {
  border-bottom-color: transparent;
}

.hse-Collapse_isOpen .hse-Collapse__contentWrapper {
  overflow: visible;
  height: auto;
  border-bottom: $collapse-border;
}

.hse-Collapse_color_invert.hse-Collapse_isOpen .hse-Collapse__contentWrapper {
  border-bottom: $collapse-invert-border;
}

.hse-Collapse_isOpen .hse-Collapse__content {
  opacity: 1;
}

.hse-Collapse_isOpen .hse-Collapse__toggleIcon.hse-Icon {
  transform: rotate(180deg);
}

.hse-Collapse_variant_content .hse-Collapse__toggleIcon {
  margin-left: $spacing-large-2x + $spacing-small-3x;
  @include respond-down('mobile-large') {
    margin-left: $spacing-small-x;
  }
}

.hse-Collapse_variant_content .hse-Collapse__content {
  @include respond-down('mobile-large') {
    margin-top: -1 * $spacing-small-3x;
    padding: 0 0 $spacing-small 0;
  }

  @include respond-up('tablet-small') {
    padding: 0 $spacing-medium $spacing-medium $spacing-medium;
  }

  @include respond-up('desktop-medium') {
    padding: 0 $spacing-medium-x $spacing-large $spacing-medium-x;
  }
}

.hse-Collapse_variant_primary ~ .hse-Collapse_variant_primary {
  margin-top: -1 * $collapse-border-size;
}
