@import '~@hse-design/tokens/lib/scss/colorsGray.scss';

@mixin disabled-state {
  &, &:hover {
    border-color: $color-gray-dusk-5;
  }

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background-color: $color-gray-morn-3;
  }
}

/** @deprecated use disabled-state */
@mixin disabledState {
  @include disabled-state;
}
