@import '../base.scss';

.hse-HeaderLanguageSelect.hse-Select {
  $width: 72px;
  width: $width;
  min-width: $width;

  .hse-Select__menu {
    width: 108px;
    margin-left: 17.5px !important; // нужно для выравнивания дропдауна по левому краю
  }

  .hse-Select__valueContainer_size_medium {
    @include text_caption_small_bold;

    &,
    .hse-Icon {
      color: $color-gray-dusk;
    }
  }

  .hse-Select__value {
    margin-right: 0;
    text-transform: uppercase;
  }

  .hse-Select__value,
  .hse-Select__valueContainer.hse-Select__valueContainer_selected,
  .hse-Select__valueContainer_selected
    .hse-Select__valueContainerIcon.hse-Icon {
    color: $color-gray-dusk;
  }
}
