@import '../../base';

.hse-FileButtonBase {
  padding: 0;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  outline: none;
  border: none;
  cursor: pointer;
  transition: background-color $transition-quick ease-out;
}

.hse-FileButtonBase:hover .hse-FileButtonBase__action {
  opacity: 1;
}

.hse-FileButtonBase__icon {
  flex-shrink: 0;
}

.hse-FileButtonBase__action {
  opacity: 0;
  display: flex;
  margin-left: auto;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  transition: opacity $transition-quick ease-out;
}

.hse-FileButtonBase__content {
  display: flex;
  white-space: nowrap;
  text-align: left;
  overflow: hidden;
}

.hse-FileButtonBase__fileName {
  overflow: hidden;
  text-overflow: ellipsis;
}

.hse-FileButtonBase__fileExtension {
  flex-shrink: 0;
}

.hse-FileButtonBase__icon,
.hse-FileButtonBase__action .hse-Icon {
  color: currentColor;
}
