@import '../../base';
@import './vars';

@mixin Button {
  @include resetDefaultAnchor;
  @include text_caption_small_bold;
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: max-content;
  padding: 0 25px;
  cursor: pointer;
  user-select: none;
  text-decoration: none;
  color: transparent;
  border: $button-border-width solid transparent;
  box-shadow: 0 0 0 2px transparent;
  appearance: none;
  background-color: rgba(255, 255, 255, 0);

  &,
  &::before {
    border-radius: $radius-small;
  }

  &::before {
    position: absolute;
    top: -1 * $button-border-width;
    right: -1 * $button-border-width;
    bottom: -1 * $button-border-width;
    left: -1 * $button-border-width;
    display: block;
    content: '';
    background-color: rgba(255, 255, 255, 0);
  }

  &,
  &:hover,
  &:focus {
    outline: none;
  }
}

@mixin Button_size_small {
  height: $button-height-small;
}

@mixin Button_size_medium {
  height: $button-height-medium;
}

@mixin Button_size_large {
  height: $button-height-large;
}

@mixin Button_focus {
  @include focus-shadow;
}

@mixin Button_disabled {
  pointer-events: none;
}

@mixin Button_variant_primary(
  $content-element-selector: '.hse-Button__content'
) {
  background-color: $color-brand;

  &:hover {
    &::before {
      background-color: rgba($color-element-overlay-dark, $opacity-60);
    }
  }

  &,
  #{$content-element-selector} {
    color: $color-base-white;
  }
}

@mixin Button_variant_primary_disabled {
  cursor: default;
  color: rgba($color-base-white, $opacity-60);
  box-shadow: none;

  &::before {
    background-color: rgba($color-element-overlay-light, $opacity-60);
  }
}

@mixin Button_variant_primary_simple_disabled {
  cursor: default;
  color: $color-base-white;
  background-color: #a1baed;
  box-shadow: none;
}

// Когда мы не можем использовать оверлей
@mixin Button_variant_primary_simple_hover {
  background-color: #1d377a; // Цвет, который получается с помощью наложения color-element-overlay-dark, 60 opacity на color-brand
}

@mixin Button_variant_secondary(
  $content-element-selector: '.hse-Button__content',
  $bubble-element-selector: '.hse-Button__bubble',
  $custom-color-modifier: '.hse-Button_custom-color'
) {
  border: 1px solid $color-gray-morn;

  #{$bubble-element-selector} {
    background: $color-brand-5;
  }

  &,
  #{$content-element-selector} {
    color: $color-gray-dusk;

    &:hover {
      color: $color-brand;
      border-color: $color-brand;
    }
  }

  #{$custom-color-modifier} {
    border-color: currentColor;

    &,
    #{$content-element-selector} {
      color: currentColor;
    }
  }
}

@mixin Button_variant_secondary_disabled(
  $content-element-selector: '.hse-Button__content'
) {
  border-color: $color-gray-morn;
  box-shadow: none;

  &,
  #{$content-element-selector} {
    color: $color-gray-dusk-3;
  }
}

@mixin Button_variant_tertiary(
  $content-element-selector: '.hse-Button__content',
  $bubble-element-selector: '.hse-Button__bubble'
) {
  color: $color-gray-dusk;
  border: 1px solid transparent;

  &:hover::before {
    background-color: $color-element-overlay-light-op90;
  }

  &:hover {
    background-color: currentColor;
  }

  &:focus-visible {
    border: 1px solid transparent;
  }

  #{$bubble-element-selector} {
    background: rgba($color-brand, $opacity-10);
  }

  #{$content-element-selector} {
    color: currentColor;
  }
}

@mixin Button_variant_tertiary_selected(
  $content-element-selector: '.hse-Button__content',
  $bubble-element-selector: '.hse-Button__bubble'
) {
  background: $color-brand;

  &:hover {
    &::before {
      background: rgba($color-element-overlay-light, $opacity-40);
    }
  }

  &:focus-visible {
    background: $color-brand;
  }

  #{$bubble-element-selector} {
    background: rgba($color-element-overlay-light, $opacity-10);
  }

  &,
  #{$content-element-selector} {
    color: $color-base-white;
  }
}

@mixin Button_variant_tertiary_selected_focused(
  $content-element-selector: '.hse-Button__content'
) {
  background: $color-brand;

  &,
  #{$content-element-selector} {
    color: $color-base-white;
  }
}

@mixin Button_variant_tertiary_focused(
  $content-element-selector: '.hse-Button__content'
) {
  border: 1px solid transparent;
  background: transparent;

  &,
  #{$content-element-selector} {
    color: $color-gray-dusk;
  }
}

@mixin Button_variant_tertiary_disabled(
  $content-element-selector: '.hse-Button__content'
) {
  &,
  #{$content-element-selector} {
    color: rgba($color-gray-dusk, $opacity-40);
  }
}

@mixin Button_variant_tertiary_inactive(
  $content-element-selector: '.hse-Button__content'
) {
  border: 1px solid transparent;
  background-color: rgba($color-gray-morn, $opacity-10);

  &,
  #{$content-element-selector} {
    color: rgba($color-gray-dusk, $opacity-40);
  }
}
