@import '../base';
$popover-width: 240px;

.hse-Popover {
  transition: opacity $transition-quick ease-out;
  pointer-events: none;
  opacity: 0;
}

.hse-Popover_active {
  pointer-events: all;
  opacity: 1;
}

.hse-Popover__title {
  @include text_caption_medium_bold;
  margin: 0 0 $spacing-small-3x 0;
}

.hse-Popover__body {
  @include text_caption_small_regular;
}

.hse-Popover__actions {
  display: flex;
  align-items: center;
  margin: $spacing-small 0 0;
}

.hse-Popover__target {
  display: inline-block;
}

.hse-Popover_withArrow {
}

.hse-Popover_withArrow .hse-PopperArrow {
  opacity: 1;
}

/// PopoverBase styles

.hse-PopoverBase {
  position: relative;
  display: block;
  width: $popover-width;
  padding: $spacing-small;
  border-radius: $radius-small;
  background: $color-background-white;
  box-shadow: #{$shadow-dropdown};
}

.hse-PopoverBase.hse-PopoverBase_noPadding {
  padding: 0;
}
