@import '../../base';
@import './vars';

@mixin Input {
  @include text_caption_medium_regular;
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  box-sizing: border-box;
  width: $input-default-width;
  min-width: $input-default-width;
  padding: 0 $spacing-small-2x;
  cursor: text;
  color: $color-base-black;
  border: $input-border;
  border-radius: $input-border-radius;
  background-color: $color-base-white;

  &:hover {
    border-color: $color-brand;
  }
}

@mixin Input_size_small {
  @include text_caption_small_regular;
  height: $input-small-height;
  padding: 0 $spacing-small-2x;
}

@mixin Input_size_medium {
  height: $input-medium-height;
  padding: $input-medium-padding;
}

@mixin Input_size_large {
  @include Input_size_medium;
  height: $input-large-height;
}

@mixin Input__input {
  @include reset-default-input;
  @include remove-autofill-style;
  position: relative;
  flex-grow: 1;
  min-width: 0;
  height: $line-height-20;
}

@mixin Input__placeholder {
  color: $color-gray-dusk;
}

@mixin Input_fullWidth {
  width: auto;
  min-width: 100%;
}

@mixin Input_focus {
  border-color: $color-brand;
}

@mixin Input_error {
  &,
  &:hover {
    border-color: $color-system-error;
  }
}

@mixin Input_disabled {
  color: $color-gray-dusk-4;
  background: $input-disabled-background;
  cursor: not-allowed;

  &,
  &:hover {
    border-color: $input-disabled-border-color;
  }
}

@mixin Input__input_disabled {
  background: $input-disabled-background;
}

@mixin Input__placeholder_disabled {
  color: $color-gray-dusk-4;
}
