@mixin RichInput {
  display: flex;
  flex-flow: column nowrap;
}

@mixin RichInput__label {
  @include text_caption_x_small_regular;
  margin-bottom: $spacing-small-3x;
  color: $color-gray-dusk;
}

@mixin RichInput__message {
  @include text_caption_small_regular;
  margin-top: $spacing-small-3x;
  color: $color-gray-dusk;
}

@mixin RichInput__hasError_message {
  color: $color-system-error;
}
