@import '../../base';
@import './vars';

@mixin Table__table {
  display: table;
}

@mixin Table__td_withoutPadding {
  @include text_paragraph_large;
  text-align: center;
  color: $color-black;
  border: 1px solid $color-morn-2;
}

@mixin Table__td {
  @include Table__td_withoutPadding;

  @include respond-up('desktop-small') {
    padding: $spacing-small-x $spacing-medium;
  }

  @include respond-up('tablet-small') {
    padding: $spacing-small;
  }

  @include respond-down('mobile-large') {
    padding: $spacing-small-3x;
  }
}
@mixin Table__thead__td_withoutPadding {
  @include Table__td_withoutPadding;
  @include text_caption_small_bold;
  color: $color-dusk;
}

@mixin Table__thead__td {
  @include Table__td;
  @include text_caption_small_bold;
  color: $color-dusk;
}

@mixin Table__thead {
  background: $table-head-background;
}

@mixin Table__tfoot {
  background: $table-foot-background;
}

@mixin Table__tr {
  &:nth-child(2n) {
    background: $table-tr-odd-background;
  }
}
