@import '~swiper/swiper.scss';
@import '../../base';

.hse-Carousel {
  position: relative;
  user-select: none;
}

.hse-Carousel_variant_fullScreen,
.hse-Carousel_variant_fullScreen .hse-CarouselSlide {
  height: 408px;
}

.hse-Carousel_variant_horizontalSingle {
  min-height: 496px;
  &.hse-Carousel_with-navigation {
    min-height: 496px + 56px;
  }

  @include respond('tablet-large') {
    min-height: 296px;
    &.hse-Carousel_with-navigation {
      min-height: 296px + 56px;
    }
  }
  @include respond('tablet-small') {
    min-height: 296px;
    &.hse-Carousel_with-navigation {
      min-height: 296px + 56px;
    }
  }
  @include respond-down('mobile-large') {
    min-height: 168px;
    &.hse-Carousel_with-navigation {
      min-height: 168px + 56px;
    }
  }
}

.hse-Carousel_variant_horizontalSingle .hse-CarouselSlide {
  height: 496px;
  @include respond('tablet-large') {
    height: 296px;
  }
  @include respond('tablet-small') {
    height: 296px;
  }
  @include respond-down('mobile-large') {
    height: 168px;
  }
}

.hse-Carousel_variant_horizontalMultiple {
  min-height: 248px;
  &.hse-Carousel_with-navigation {
    min-height: 248px + 56px;
    @include respond-down('tablet-large') {
      min-height: 168px + 56px;
    }
  }
  @include respond-down('tablet-large') {
    min-height: 168px;
  }
}

.hse-Carousel_variant_horizontalMultiple .hse-CarouselSlide {
  height: 248px;
  @include respond-down('tablet-large') {
    height: 168px;
  }
}

.hse-Carousel__container {
  height: 100%;
  cursor: grab;
}

.hse-Carousel__navigation {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-start;
}

.hse-Carousel_variant_fullScreen .hse-Carousel__navigation,
.hse-Carousel_variant_horizontalSingle .hse-Carousel__navigation {
  align-items: center;
}

.hse-Carousel_variant_fullScreen .hse-Carousel__navigation {
  margin-top: -3 * $sizing-40;
}

.hse-Carousel_variant_horizontalSingle .hse-Carousel__navigation,
.hse-Carousel_variant_horizontalMultiple .hse-Carousel__navigation {
  margin-top: $spacing-small-x;
}

.hse-Carousel__navigationButtons {
  display: flex;
  align-items: center;
}

.hse-Carousel_variant_horizontalSingle .hse-Carousel__navigationButtons {
  @include respond-down('tablet-large') {
    display: none;
  }
}

.hse-Carousel__navigationButton {
  &:not(:last-child) {
    margin-right: $spacing-small-x;
  }
}

.hse-Carousel .hse-Carousel__navigationButton .hse-IconButton .hse-Icon,
.hse-Carousel .hse-Carousel__navigationText {
  color: $color-dusk;
}

.hse-Carousel.hse-Carousel_variant_fullScreen
  .hse-Carousel__navigationButton
  .hse-IconButton
  .hse-Icon,
.hse-Carousel.hse-Carousel_variant_fullScreen .hse-Carousel__navigationText {
  color: $color-light;
}

.hse-Carousel__navigationText {
  @include text_caption_small_regular;
  display: flex;
  align-items: center;
  margin: 0 0 0 $spacing-small + $spacing-small-3x / 2;
  user-select: none;
}

.hse-Carousel__navigationTextDelimiter {
  display: block;
  margin: 0 $spacing-small-3x / 2;
}

.hse-Carousel.hse-Carousel:not(.hse-Carousel_variant_fullScreen)
  .hse-CarouselSlide__content,
.hse-Carousel.hse-Carousel:not(.hse-Carousel_variant_fullScreen)
  .hse-CarouselSlide__image::before {
  display: none;
}

.hse-Carousel__footerContent {
  display: grid;
  grid-template-columns: max-content auto;
  grid-column-gap: $spacing-medium;
}

.hse-Carousel_variant_horizontalMultiple .hse-Carousel__footerContent,
.hse-Carousel_variant_horizontalSingle .hse-Carousel__footerContent {
  @include respond-down('tablet-large') {
    justify-content: center;
    grid-template-columns: auto;
  }
}
.hse-Carousel_variant_horizontalMultiple .hse-Carousel__navigationText,
.hse-Carousel_variant_horizontalSingle .hse-Carousel__navigationText {
  @include respond-down('tablet-large') {
    margin: 0;
  }
}
