@import '../../base';

@mixin TooltipTarget {
  display: inline-block;
  vertical-align: middle;
}

@mixin Tooltip {
  user-select: none;
  transition: opacity $transition-quick ease-out;
  pointer-events: none;
  opacity: 0;
}

@mixin Tooltip_visible {
  user-select: inherit;
  pointer-events: inherit;
}

@mixin Tooltip_closable__content {
  padding-right: $spacing-medium-x;
}

@mixin Tooltip_variant_brand {
  background: $color-brand-5;
}

@mixin Tooltip_variant_black {
  color: $color-base-white;
  background: $color-base-black;
}

@mixin Tooltip_variant_invert {
  color: $color-base-black;
  background: $color-base-white;
}

@mixin Tooltip__container {
  position: relative;
}

@mixin Tooltip__content {
  @include text_caption_small_regular;
  padding: $spacing-small-x;
  color: $color-base-black;
  border-radius: $radius-large;
}

@mixin Tooltip__closeButton {
  position: absolute;
  top: $spacing-small-3x;
  right: $spacing-small-3x;
}
