@import '../../base';
@import 'vars';

@mixin hse-DropdownMenu {
  display: flex;
  flex-direction: column;
  z-index: $z-index-dropdown-menu;
  background-color: $color-background-white;
  border: 1px solid $color-gray-dusk-5;
  border-radius: $radius-small;
}

@mixin hse-DropdownMenu__items {
  overflow-y: auto;
  max-height: $dropdown-menu-max-height;
  padding: $spacing-small-2x;
}

@mixin hse-DropdownMenuBaseItem {
  @include text_caption_medium_regular;
  position: relative;
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  box-sizing: border-box;
  min-height: $sizing-40;
  padding: $spacing-small-2x;
  cursor: pointer;
  color: $color-base-black;
  border-radius: $radius-small;

  &:hover {
    @include hse-DropdownMenuBaseItem_hover;
  }
}

@mixin hse-DropdownMenuBaseItem_hover {
  background-color: $color-background-gray-1;
}

@mixin hse-DropdownMenuBaseItem_selected {
  color: $color-base-white;
  background-color: $color-brand;
}

@mixin hse-DropdownMenuBaseItem_hover_selected {
  background-color: $color-brand-dark;
}

@mixin hse-DropdownMenuBaseItem_focused {
  @include focus-shadow;
  background-color: $color-background-gray-1;
}

@mixin hse-DropdownMenuBaseItem_selected_focused {
  background-color: $color-brand;
}
