@import '../base.scss';

.hse-Header {
  @include text-caption_small_bold;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: $header-height;
  padding: 0 $spacing-large 0 $spacing-small-x;
  color: $color-gray-dusk;
  border-bottom: 1px solid $color-gray-morn-3;
  background-color: $color-base-white;

  @include respond('desktop-medium') {
    padding: 0 $spacing-small 0 $spacing-small-x;
  }

  @include respond-down('tablet-small') {
    @include text_label_x_small_bold;
    padding: 0 $spacing-medium 0 $spacing-small-x;
  }

  @include respond-down('mobile-large') {
    height: $header-height-small;
    padding: $spacing-small-2x $spacing-small;
  }
}

.hse-Header__logo,
.hse-Header__left {
  display: flex;
  align-items: center;
  @include respond-down('mobile-large') {
    align-items: flex-start;
  }
}

.hse-Header__logo {
  display: flex;
  align-items: center;
  @include respond-down('mobile-large') {
    align-items: flex-start;
    flex-direction: column;
  }
}

.hse-Header__title {
  white-space: nowrap;
}

.hse-Header__left {
  justify-content: space-between;
  width: 100%;
  padding: 0 0 0 $spacing-large - $spacing-small-3x;
  white-space: nowrap;
  @include respond-down('mobile-large') {
    display: none;
  }
}

.hse-Header__right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-left: auto;
  white-space: nowrap;
}

.hse-Header__hamburger {
  display: flex;
  align-items: center;
}

.hse-Header__hamburger .hse-Icon {
  color: $color-gray-dusk;
}

.hse-HeaderDivider.hse-Divider.hse-Divider_variant_vertical {
  height: $sizing-16;
  margin: 0 $spacing-small-3x;
}

.hse-Header .hse-ClickableIcon,
.hse-Header .hse-Icon {
  color: $color-gray-dusk;
}
