@import '../base';

$modal-vertical-padding: 40px;

.hse-BaseModal {
  position: relative;
  width: 600px;

  @include respond-up('tablet-small') {
    max-height: 100vh;
  }

  @include respond-down('mobile-large') {
    width: 100%;
  }
}

.hse-BaseModal__closeButton {
  position: absolute;
  z-index: 1;
  top: $spacing-small-3x;
  right: $spacing-small-3x;
  display: block;
}

.hse-BaseModal__closeButton.hse-IconButton .hse-Icon {
  color: $color-morn;
}

.hse-ModalOverlay,
.ReactModal__Overlay {
  position: fixed;
  z-index: $z-index-modal;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  overflow: auto;
  align-items: center;
  justify-content: center;
  outline: none;
  background: $color-screen-overlay-dark;
  overscroll-behavior: contain;
}

.hse-ModalOverlay__content,
.ReactModal__Content {
  outline: none;
}

.hse-BaseModal__wrapper {
  position: relative;
  display: block;
  overflow: auto;
  min-height: 300px;
  padding: $spacing-medium;
  border-radius: $radius-large;
  background: $color-background-white;

  @include respond-up('tablet-small') {
    margin: $modal-vertical-padding 0;
  }
  @include respond('mobile-large') {
    padding: $spacing-medium $spacing-small;
  }
  @include respond('mobile-small') {
    padding: $spacing-small-x $spacing-small-x $spacing-medium;
  }
}

.hse-ModalForm {
}

.hse-ModalForm__content {
  @include text_caption_large_regular;
}

.hse-ModalForm__actions {
  display: flex;
  justify-content: flex-end;
  margin: $spacing-small-x 0 0;
  text-align: right;
}

.hse-ModalForm__title,
.hse-ModalInfo__title {
  @include text_heading_h3;
  margin: 0 0 $spacing-small-x;

  @include respond-down('mobile-large') {
    @include text_heading_h4;
  }
}

.hse-ModalStatus .hse-BaseModal__wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: 448px;
}

.hse-ModalStatus__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto $spacing-medium;
}

.hse-ModalStatus__title {
  @include text_heading_h4;
  text-align: center;
}

.hse-ModalStatus__actions {
  display: flex;
  justify-content: center;
  margin: $spacing-medium 0 0;
}
