@import '../../base';

.hse-Toast.hse-BaseAlert,
.hse-Toast.hse-BaseAlert_hasDescription {
  border-radius: $radius-medium;
  padding: $spacing-small-x $spacing-medium + $spacing-small $spacing-small-x
    $spacing-small-x;
}

.hse-BaseAlert.hse-Toast:not(.hse-BaseAlert_hasDescription)
  .hse-BaseAlert__wrapper {
  justify-content: flex-start;
  align-items: center;

  @include respond-down('mobile-large') {
    align-items: flex-start;
  }
}

.hse-Toast_variant_info {
  background: $color-brand-5;
  color: $color-text-brand-dark;
}

.hse-Toast_variant_info .hse-BaseAlert__icon,
.hse-Toast_variant_info .hse-BaseAlert__wrapper > *:first-child {
  color: $color-brand;
}

.hse-Toast_variant_success {
  background: $color-system-success-light;
  color: $color-green-deepdark;
}

.hse-Toast_variant_success .hse-BaseAlert__icon,
.hse-Toast_variant_success .hse-BaseAlert__wrapper > *:first-child {
  color: $color-system-success-dark;
}

.hse-Toast_variant_error {
  background: $color-system-error-light;
  color: $color-text-red-deepdark;
}

.hse-Toast_variant_error .hse-BaseAlert__icon,
.hse-Toast_variant_error .hse-BaseAlert__wrapper > *:first-child {
  color: $color-system-error-dark;
}

.hse-Toast_variant_warning {
  background: $color-system-warning-light;
  color: $color-orange-deepdark;
}

.hse-Toast_variant_warning .hse-BaseAlert__icon,
.hse-Toast_variant_warning .hse-BaseAlert__wrapper > *:first-child {
  color: $color-warning-dark;
}

.hse-Toast .hse-BaseAlert__description {
  color: $color-text-base-black;
}

.hse-Toast .hse-BaseAlert__content {
  padding: 0 $spacing-small-x 0 0;

  @include respond-down('mobile-small') {
    padding: 0;
  }
}

.hse-Toast.hse-BaseAlert_hasDescription .hse-BaseAlert__content {
  @include respond('tablet-large') {
    padding: 0 $spacing-large + $spacing-small-3x 0 0;
  }
  @include respond('tablet-small') {
    padding: 0 $spacing-large + $spacing-small-3x 0 0;
  }
  @include respond('mobile-large') {
    padding: 0 $spacing-large + $spacing-small-3x 0 0;
  }
  @include respond-down('mobile-small') {
    padding: 0 $spacing-medium-x 0 0;
  }
}

.hse-Toast .hse-BaseAlert__title {
  @include respond-down('mobile-small') {
    padding: 0 $spacing-small-2x 0 0;
  }
}

.hse-Toast.hse-BaseAlert_hasDescription .hse-BaseAlert__description {
  @include respond-up('desktop-medium') {
    max-width: 80.5%;
  }
}

.hse-Toast.hse-BaseAlert_hasDescription .hse-BaseAlert__closeButton {
  position: absolute;
  top: $spacing-small-x;
  right: $spacing-small-x;
  @include respond-down('mobile-large') {
    right: $spacing-small-x;
  }
}

.hse-Toast .hse-BaseAlert__icon {
  margin-right: $spacing-small-x;
}
