@import '../base';

.hse-ProgressBar {
  position: relative;
  display: block;
  overflow: hidden;
  width: 100%;
  height: 4px;
  border-radius: $radius-small / 2;
  background-color: $color-background-gray-1;
}

.hse-ProgressBar_size_small {
  height: 2px;
}

.hse-ProgressBar__fill {
  height: 100%;
  transition: width 300ms linear;
  border-radius: $radius-small / 2;
  background-color: $color-brand;
}

/// indeterminate

.hse-ProgressBar_indeterminate .hse-ProgressBar__fill {
  background: transparent;
}

.hse-ProgressBar__indeterminate {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.hse-ProgressBar__indeterminateLine {
  position: absolute;
  top: 0;
  right: auto;
  bottom: 0;
  left: 0;
  width: auto;
  height: inherit;
  animation-duration: 2.2s;
  animation-iteration-count: infinite;
  background-color: $color-brand;
  will-change: left, right;
}

.hse-ProgressBar__indeterminateLine_short {
  animation-name: progressbar-indeterminate-short;
}

.hse-ProgressBar__indeterminateLine_long {
  animation-name: progressbar-indeterminate-long;
}

@keyframes progressbar-indeterminate-long {
  0% {
    right: 100%;
    left: -90%;
  }
  60% {
    right: 100%;
    left: -90%;
  }
  100% {
    right: -35%;
    left: 100%;
  }
}

@keyframes progressbar-indeterminate-short {
  0% {
    right: 100%;
    left: -200%;
  }
  60% {
    right: -8%;
    left: 107%;
  }
  100% {
    right: -8%;
    left: 107%;
  }
}
