@import '../../base';

.hse-FileButton {
  @include resetDefaultAnchor;
  color: $color-brand;
}

.hse-FileButton:hover,
.hse-FileButton:focus-visible {
  background: $color-brand-5;
}

.hse-FileButton_size_large {
  height: $sizing-48;
}

.hse-FileButton_size_medium {
  height: $sizing-40;
}

.hse-FileButton_size_small {
  height: $sizing-32;
}

.hse-FileButton__icon {
  margin-right: $spacing-small-3x;
}

.hse-FileButton__content {
  @include text_caption_small_bold;
}

.hse-FileButton__fileName {
  overflow: hidden;
  text-overflow: ellipsis;
}
