@import '../../base';

@mixin TextArea {
  position: relative;
  padding: 0;
}

@mixin TextArea__textarea {
  @include reset-default-input;
  @include remove-autofill-style;
  position: relative;
  width: 100%;
  height: 100%;
  min-height: $sizing-48;
  padding: $spacing-small-2x;
  resize: vertical;
  color: $color-black;

  &::placeholder {
    color: $color-gray-dusk;
  }
}
