@import '../../base';
@import 'vars';
@import 'mixins';

.hse-Popper {
  @include Popper;
}

.hse-Popper .hse-PopperArrow {
  @include PopperArrow;
}

.hse-Popper[data-popper-placement^='right'] .hse-PopperArrow {
  svg {
    transform: rotate(90deg) translateY(10px);
  }
}

.hse-Popper[data-popper-placement^='bottom'] .hse-PopperArrow {
  top: 0;

  svg {
    transform: rotate(180deg) translateY(100%);
  }
}

.hse-Popper[data-popper-placement^='left'] .hse-PopperArrow {
  right: 0;
  svg {
    transform: translateX(10px) rotate(-90deg);
  }
}
