@import '../base';
@import '../baseText';

.hse-LabeledProgressBar {
  width: 480px;
  padding: $spacing-small-x $spacing-small;
}

.hse-LabeledProgressBar__label {
  @include text_caption_medium_bold;
  position: relative;
  width: 100%;
  height: $sizing-16;
  margin-bottom: $spacing-small-x;
  color: $color-base-black;
  line-height: $line-height-16;
}

.hse-LabeledProgressBar__label_size_small {
  @include text_caption_small_bold;
  margin-bottom: $spacing-small-3x;
  line-height: $line-height-16;
}

.hse-LabeledProgressBar__cancelIcon,
.hse-LabeledProgressBar__cancelIcon:focus,
.hse-LabeledProgressBar__cancelIcon:focus-visible,
.hse-LabeledProgressBar__cancelIcon.hse-ClickableIcon {
  display: flex;
  position: absolute;
  padding: 0;
  margin: 0;
  right: 0;
  top: 0;
}

.hse-LabeledProgressBar__description {
  @include text_caption_medium_regular;
  width: 100%;
  height: 20px;
  margin-top: $spacing-small-x;
  color: $color-gray-dusk;
}

.hse-LabeledProgressBar__description_size_small {
  @include text_caption_small_regular;
  margin-top: $spacing-small-3x;
}
