// @define Plug

@import '../../base';

.hse-Plug {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.hse-Plug__text {
  text-align: center;
}

.hse-Plug__title {
  @include text_heading_h4;
  margin: 0 0 $spacing-small-x;
  color: $color-base-black;
}

.hse-Plug__description {
  @include text_caption_large_regular;
  color: $color-base-black;
}

.hse-Plug__descriptionParagraph {
  margin: 0;
  color: $color-base-black;
}

.hse-Plug__button {
  margin: $spacing-medium 0 0;
}

.hse-Plug__icon {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 0 $spacing-medium;
  background-size: contain;
}

.hse-Plug__icon.hse-Plug__icon_ufo {
  /** todo придумать, что делать с путями */
  height: 96px;
  background: url('./assets/images/ufo.svg') no-repeat center center;
}

.hse-Plug__icon.hse-Plug__icon_cat {
  /** todo придумать, что делать с путями */
  height: 96px;
  background: url('./assets/images/cat.svg') no-repeat center center;
}
