@import './vars';

@mixin IconButton {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  outline: none;

  &::before {
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    display: block;
    content: '';
    background: transparent;
  }
}

@mixin IconButton_size_small {
  width: $iconbutton-size-small-width;
  height: $iconbutton-size-small-height;
  &,
  &::before {
    border-radius: $iconbutton-size-small-radius;
  }
}

@mixin IconButton_size_medium {
  width: $iconbutton-size-medium-width;
  height: $iconbutton-size-medium-height;
  &,
  &::before {
    border-radius: $iconbutton-size-medium-radius;
  }
}

@mixin IconButton_size_large {
  width: $iconbutton-size-large-width;
  height: $iconbutton-size-large-height;
  &,
  &::before {
    border-radius: $iconbutton-size-large-radius;
  }
}

@mixin IconButton_disabled {
  border-color: $color-gray-morn-3;
  background: $color-gray-morn-3;
}

@mixin IconButton_variant_secondary(
  $inactive-selector: '.hse-IconButton_inactive',
  $disabled-selector: '.hse-IconButton_disabled',
  $selected-selector: '.hse-IconButton_selected'
) {
  color: $color-brand;
  background: currentColor;
  border: none;
  &:not(#{$inactive-selector}):not(#{$disabled-selector}):not(#{$selected-selector}):not(:hover)::before {
    background: rgba(
                    $color-element-overlay-light,
                    0.95
    ); // todo: add token .95?
  }
}

@mixin IconButton_variant_secondary_hover {
  border-width: 0;
  &::before {
    background: $color-element-overlay-light-op90;
  }
}

@mixin IconButton_variant_secondary_disabled(
  $content-selector: '.hse-IconButton__content'
) {
  @include IconButton_disabled;
  #{$content-selector} {
    color: $color-gray-morn;
  }
}

@mixin IconButton_variant_secondary_inactive(
  $content-selector: '.hse-IconButton__content'
) {
  border-color: $color-gray-dusk-5;
  background: $color-gray-dusk-5;
  #{$content-selector} {
    color: $color-gray-dusk-3;
  }
}

@mixin IconButton_variant_secondary_selected(
  $content-selector: '.hse-IconButton__content'
) {
  #{$content-selector} {
    color: $color-base-white;
  }
}

@mixin IconButton_variant_secondary__content {
  color: $color-brand;
}
