@import '../../base';

$input-small-height: $sizing-32;
$input-medium-height: $sizing-40;
$input-large-height: $sizing-48;
$input-medium-padding: 0 $spacing-small-x;
$input-default-width: 280px;
$input-disabled-background: $color-gray-morn-3;
$input-border-color: $color-gray-morn;
$input-disabled-border-color: $color-gray-dusk-5;
$input-border: 1px solid $input-border-color;
$input-border-radius: $radius-small;
