// @define Checkbox

@import '../../base';
@import './CheckboxIconMixins';
@import './CheckboxMixins';

.hse-Checkbox {
  @include Checkbox;
}

.hse-Checkbox:hover .hse-Checkbox__icon {
  @include CheckboxIcon_hover;
}

.hse-Checkbox__input {
  @include hideDefaultInput;
}

.hse-Checkbox__label {
  @include Checkbox__label;
}

.hse-Checkbox_checked:hover
  .hse-Checkbox__icon:not(.hse-BaseCheckboxIcon_radio) {
  @include CheckboxIcon_checked_hover;
}

.hse-Checkbox_checked:hover .hse-BaseCheckboxIcon_radio {
  @include CheckboxIcon_radio_checked_hover;
}

.hse-Checkbox_inactive {
  @include Checkbox_inactive;
}

.hse-Checkbox_inactive .hse-Checkbox__label {
  @include Checkbox__label_inactive;
}

.hse-Checkbox_inactive:hover .hse-Checkbox__icon {
  @include CheckboxIcon_inactive;
}

.hse-Checkbox_radio.hse-Checkbox_checked.hse-Checkbox_inactive:hover
  .hse-BaseCheckboxIcon_radio {
  @include CheckboxIcon_radio_checked_inactive;
}
