/* stylelint-disable no-descending-specificity */

@import '../../base';
@import './mixins';
@import './vars';

.hse-Link {
  @include reset-button;
  @include Link;
}

.hse-Link_standalone {
  @include text_paragraph_large;
}

.hse-Link_inactive {
  @include Link_inactive;
}

.hse-Link_variant_primary {
  @include Link_variant_primary;
}

.hse-Link_variant_secondary {
  &,
  &:visited {
    color: $color-brand;

    @include Link_hover {
      color: rgba($color-brand, $opacity-60);
    }
  }

  &.hse-Link_inactive {
    &,
    &:visited {
      color: $color-gray-dusk-3;
    }
  }
}

.hse-Link_variant_secondaryInvert {
  &,
  &:visited {
    color: rgba($color-element-overlay-light, $opacity-60);

    &:not(.hse-Link_inactive) {
      &:hover,
      &:active {
        color: $color-base-white;
      }

      &:not(.hse-Link_controlledFocus).focus-visible,
      &.hse-Link_focused {
        color: $color-base-white;
      }
    }
  }
}

.hse-Link_variant_secondaryInvert.hse-Link_inactive {
  &,
  &:visited {
    color: rgba($color-element-overlay-light, $opacity-40);
    border-bottom: 1px solid transparent;
  }
}

.hse-Link_variant_tertiary {
  &,
  &:visited {
    color: $color-gray-dusk;
    font-weight: $font-weight-600;
  }

  &.hse-Link_inactive {
    color: $color-gray-dusk-3;
  }

  @include Link_hover {
    color: $color-brand;
  }

  @include Link_focus {
    color: $color-brand;
  }
}

.hse-Link_underlined {
  &,
  &:active,
  &:visited {
    border-bottom: 1px solid currentColor;
  }
  @include Link_hover {
    border-bottom: 1px solid currentColor;
  }
}

// showVisited prop styles

.hse-Link.hse-Link_variant_primary.hse-Link_showVisited:visited {
  &,
  &:hover,
  &:active {
    color: $color-gray-dusk-1;
    border-bottom-color: $color-accent-purple;
  }
}

.hse-Link.hse-Link_showVisited:visited,
.hse-Link.hse-Link_showVisited:visited:hover,
.hse-Link.hse-Link_showVisited:visited:active,
.hse-Link.hse-Link_showVisited:visited:not(.hse-Link_controlledFocus).focus-visible,
.hse-Link.hse-Link_showVisited.hse-Link_focused:visited {
  &.hse-Link_variant_secondary,
  &.hse-Link_variant_tertiary {
    color: $color-accent-purple;
  }
}

.hse-Link.hse-Link_variant_secondaryInvert.hse-Link_showVisited:visited,
.hse-Link.hse-Link_variant_secondaryInvert.hse-Link_showVisited:visited:hover,
.hse-Link.hse-Link_variant_secondaryInvert.hse-Link_showVisited:visited:active,
.hse-Link.hse-Link_variant_secondaryInvert.hse-Link_showVisited:visited:not(.hse-Link_controlledFocus).focus-visible,
.hse-Link.hse-Link_variant_secondaryInvert.hse-Link_showVisited.hse-Link_focused:visited {
  color: $color-accent-purple-3;
}

/// Слоты
.hse-Link__left {
  margin-right: $link-side-slot-margin;
}

.hse-Link__right {
  margin-left: $link-side-slot-margin;
}

.hse-Link_variant_tertiary.hse-Link_selected {
  color: $color-brand;
  @include Link_hover {
    color: $color-brand-dark;
  }
  @include Link_focus {
  }
}
