@import '../base';

.hse-LabeledSpinner {
  display: flex;
  flex-flow: row nowrap;
  padding: $spacing-small-x;
  border-radius: $radius-small;
  background: $color-base-white;
}

.hse-LabeledSpinner__info {
  margin-left: $spacing-small-x;
}

.hse-LabeledSpinner__label {
  @include text_caption_small_bold;
  margin-bottom: $spacing-small-3x / 2;
  color: $color-base-black;
}

.hse-LabeledSpinner__description {
  @include text_caption_small_regular;
  color: $color-gray-dusk;
}

.hse-LabeledSpinner_size_small {
}

.hse-LabeledSpinner_size_medium {
  .hse-LabeledSpinner__label {
    @include text_caption_large_bold;
    margin-bottom: $spacing-small-3x;
  }

  .hse-LabeledSpinner__description {
    @include text_caption_large_regular;
  }
}
