// @define Icon

@import '../base';

.hse-Icon {
  color: $color-base-black;
}

@for $i from 1 through length($iconSizeTypes) {
  // stylelint-disable-next-line plugin/selector-bem-pattern
  .hse-Icon_size_#{nth($iconSizeTypes, $i)} {
    width: nth($iconSizes, $i);
    height: nth($iconSizes, $i);
  }
}
