@import '../base';

.hse-BaseAlert {
  position: relative;
  width: 100%;
  min-height: 52px;
  color: $color-text-brand-dark;
  background: $color-brand-5;
}

.hse-BaseAlert__wrapper {
  display: flex;
  width: 100%;
}

.hse-BaseAlert_hasDescription {
  align-items: flex-start;
  padding: $spacing-small-x $spacing-large $spacing-small $spacing-large-3x +
    $spacing-small-x;
}

.hse-BaseAlert__content {
  display: flex;
  align-items: baseline;
  align-self: center;
  width: 100%;

  @include respond-down('tablet-large') {
    flex-direction: column;
  }
}

.hse-BaseAlert_hasDescription .hse-BaseAlert__content {
  flex-direction: column;
}

.hse-BaseAlert__title {
  @include text_caption_medium_bold;
}

.hse-BaseAlert__description {
  @include text_caption_small_regular;
  margin: $spacing-small-3x 0 0;
}

.hse-BaseAlert__icon {
  flex: 0 0 $sizing-24;
  margin-right: $spacing-small-x;
  color: $color-brand;

  @include respond-down('tablet-large') {
    margin-right: $spacing-small-3x;
  }
}

.hse-BaseAlert__actions {
  @include text_caption_medium_regular;
  display: flex;
  align-items: baseline;
  margin: 0 0 0 $spacing-medium-x;
}

.hse-BaseAlert_hasDescription .hse-BaseAlert__actions {
  margin: $spacing-small-x 0 0;
}

.hse-BaseAlert:not(.hse-BaseAlert_hasDescription) .hse-BaseAlert__actions {
  @include respond-down('tablet-large') {
    margin: $spacing-small-x 0 0;
  }
}

.hse-BaseAlert__actions > *:not(:last-child) {
  margin-right: $spacing-small-x;
}
