@import '../../base';
@import './BadgeLabelMixins';

.hse-BadgeLabel {
  @include reset-button;
  position: relative;
  display: inline-flex;
  overflow: hidden;
  align-items: center;
  box-sizing: border-box;
  min-width: fit-content;
  transition: color $transition-quick ease-out,
    border-color $transition-quick ease-out,
    background-color $transition-quick ease-out,
    box-shadow $transition-quick ease-out;
  vertical-align: middle;
  white-space: nowrap;
  border: 1px solid transparent;
  border-radius: $sizing-16;
  outline: none;
}

.hse-BadgeLabel_clickable:not(.hse-BadgeLabel_disabled) {
  cursor: pointer;
}

.hse-BadgeLabel_variant_outlined {
  color: $color-gray-dusk;
  background-color: $color-background-white;
}

.hse-BadgeLabel_variant_outlined:not(.hse-BadgeLabel_selected) {
  border: 1px solid currentColor;
}

.hse-BadgeLabel_variant_filled {
  color: $color-base-white;
  border-color: transparent;
  background-color: $color-gray-dusk;
}

.hse-BadgeLabel_size_smallX {
  @include BadgeLabel_size_small_x;
}

.hse-BadgeLabel_size_small {
  @include BadgeLabel_size_small;
}

.hse-BadgeLabel_size_medium {
  @include BadgeLabel_size_medium;
}

.hse-BadgeLabel_variant_filled.hse-BadgeLabel_disabled {
  padding-top: 2px;
  padding-bottom: 2px;
  color: $color-base-white;
  background-color: $color-gray-dusk-4;
}

.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_disabled {
  color: $color-gray-dusk-4;
}

.hse-BadgeLabel_variant_filled.hse-BadgeLabel_hover,
.hse-BadgeLabel_variant_filled.hse-BadgeLabel_clickable:not(.hse-BadgeLabel_disabled):hover {
  border-color: $color-brand-dark;
  background-color: $color-brand-dark;
}

.hse-BadgeLabel_variant_filled.hse-BadgeLabel_focused,
.hse-BadgeLabel_variant_filled.hse-BadgeLabel_clickable:focus-visible {
  @include focus-shadow;
  background-color: $color-brand-dark;
}

.hse-BadgeLabel_variant_filled.hse-BadgeLabel_clickable:active:not(.hse-BadgeLabel_disabled) {
  background: $color-brand-dark;
}

.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_hover,
.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_clickable:not(.hse-BadgeLabel_disabled):hover {
  color: $color-brand;
}

.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_focused,
.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_clickable:focus-visible {
  @include focus-shadow;
  color: $color-brand;
}

.hse-BadgeLabel_variant_outlined.hse-BadgeLabel_clickable .hse-Button__bubble {
  background: $color-brand-5; // todo bubble & bubble-inverted
}

.hse-BadgeLabel_selected {
  color: $color-base-white;
  background-color: $color-brand;
}

.hse-BadgeLabel__bubbleRoot {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
