@import '../base';

$spinner-rotate-animation: spinner-rotate 1.4s linear infinite;
$spinner-rotate-dash: spinner-dash 1.4s ease-in-out infinite;
$spinner-rotate-dash-done: spinner-dash-done 0.9s ease-in-out forwards;
$spinner-loading-svg-transition: all 0.2s ease-in-out;
$spinner-overlay-transition: all 0.6s ease-in-out;

.hse-Spinner {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;

  &:hover {
    * {
      color: var(--spinner-color-hover, $color-brand-dark);
    }
  }

  * {
    color: var(--spinner-color, $color-brand);
  }
}

.hse-Spinner__overlay {
  z-index: 2;
  transition: $spinner-overlay-transition;
  stroke: currentColor;
}

.hse-Spinner__cancel {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.hse-Spinner.hse-Spinner_without-done-fill-animation.hse-Spinner_done
  .hse-Spinner__checkmark {
  animation: none;
}

.hse-Spinner__checkmark {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 50%;
  stroke-width: 2;
  stroke: currentColor;
  stroke-miterlimit: 10;
}

.hse-Spinner__checkmark-check {
  transform-origin: 50% 50%;
  animation: checkmark-stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
}

.hse-Spinner_done {
  .hse-Spinner__checkmark {
    animation: checkmark-fill 0.4s ease-in-out 0.4s forwards,
    scale 0.3s ease-in-out 0.9s both;
    box-shadow: inset 0 0 0 currentColor;
  }

  .hse-Spinner__overlay {
    animation: $spinner-rotate-dash-done;
  }
}

.hse-Spinner_loading {
  & > .hse-Spinner__checkmark {
    transition: $spinner-loading-svg-transition;
    transform-origin: center center;
    animation: $spinner-rotate-animation;
  }

  .hse-Spinner__overlay {
    animation: $spinner-rotate-dash;
    stroke-linecap: round;
    stroke-dasharray: 80, 200;
    stroke-dashoffset: 0;
  }
}

@keyframes checkmark-fill {
  100% {
    box-shadow: var(
      --checkmark-fill-box-shadow,
      inset 0 0 0 30px var(--checkmark-fill-color, #fff)
    );
  }
}

@keyframes checkmark-stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes spinner-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 100, 200;
    stroke-dashoffset: -15px;
  }

  100% {
    stroke-dasharray: 100, 200;
    stroke-dashoffset: -125px;
  }
}

@keyframes spinner-dash-done {
  0% {
    stroke-dasharray: 0, 200;
    stroke-dashoffset: 0;
  }

  100% {
    stroke-dasharray: 200, 200;
    stroke-dashoffset: 0;
  }
}

@keyframes spinner-rotate {
  100% {
    transform: rotate(360deg);
  }
}
