// @define Divider

@import '../../base';
@import './vars';

.hse-Divider {
  margin: 0;
  border: 0;
  background-color: $divider-color-light;
}

.hse-Divider_variant_horizontal {
  height: 1px;
}

.hse-Divider_variant_vertical {
  width: 1px;
}

.hse-Divider_color_light {
  background-color: $divider-color-light;
}

.hse-Divider_color_dark {
  background-color: $divider-color-dark;
}
