@import '../base';

.hse-BadgeIcon {
  @include reset-button;
  display: inline-flex;
  align-items: center;
  padding: $spacing-small-3x / 2;
  vertical-align: middle;
  white-space: nowrap;
}
.hse-BadgeIcon__avatar {
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  color: $color-gray-dusk;
  background-color: $color-gray-morn-3;
}
.hse-BadgeIcon__avatar_size_small {
  @include text_label_x_small_bold;
  width: $sizing-24;
  min-width: $sizing-24;
  height: $sizing-24;
  border-radius: $radius-small;
}
.hse-BadgeIcon__avatar_size_medium {
  @include text_caption_x_small_bold;
  width: $sizing-32;
  min-width: $sizing-32;
  height: $sizing-32;
  border-radius: $radius-medium;
}
.hse-BadgeIcon__avatar_size_large {
  @include text_caption_medium_bold;
  width: $sizing-48;
  min-width: $sizing-48;
  height: $sizing-48;
  border-radius: $radius-large;
}

.hse-BadgeIcon__avatar_image {
  width: 100%;
  height: 100%;
  background-color: $color-gray-morn;
  object-fit: cover;
}

.hse-BadgeIcon__avatar_text,
.hse-BadgeIcon__avatar_icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  text-transform: uppercase;
}

.hse-BadgeIcon__avatar_icon .hse-Icon {
  color: inherit;
}

.hse-BadgeIcon__label {
  padding-left: $spacing-small-3x;
  vertical-align: middle;
  color: $color-base-black;
}
.hse-BadgeIcon__label_size_small {
  @include text_label_x_small_bold;
  padding-left: $spacing-small-3x / 2;
}
.hse-BadgeIcon__label_size_medium {
  @include text_caption_small_bold;
}
.hse-BadgeIcon__label_size_large {
  @include text_caption_medium_bold;
}
.hse-BadgeIcon__avatar_online {
  position: absolute;
  right: 0;
  bottom: 0;
  border: 1px solid $color-base-white;
}
.hse-BadgeIcon__avatar_size_small .hse-BadgeIcon__avatar_online {
  right: 0;
  bottom: -0.5px;
}
.hse-BadgeIcon__avatar_size_medium .hse-BadgeIcon__avatar_online {
  right: 1px;
  bottom: 1px;
}
.hse-BadgeIcon__avatar_size_large .hse-BadgeIcon__avatar_online {
  right: 2px;
  bottom: 2px;
}

.hse-BadgeIcon_variant_clickable {
  cursor: pointer;
  user-select: none;
  border: none;
  background-color: transparent;

  &:hover {
    background-color: $color-gray-morn-3;
  }

  &:focus-visible {
    outline: none;
    &:not(.hse-BadgeIcon_disabled) {
      @include focus-shadow;
      background-color: $color-gray-morn-3;
    }
  }
}

.hse-BadgeIcon_variant_clickable.hse-BadgeIcon_size_small {
  height: $sizing-32;
  border-radius: $radius-medium;
}
.hse-BadgeIcon_variant_clickable.hse-BadgeIcon_size_medium {
  height: $sizing-40;
  border-radius: $radius-large;
}
.hse-BadgeIcon_variant_clickable.hse-BadgeIcon_size_large {
  height: calc(#{$sizing-48} + #{$spacing-small-3x});
  border-radius: $radius-medium * 2;
}
.hse-BadgeIcon_variant_clickable .hse-BadgeIcon__label_size_small {
  @include text_caption_x_small_bold;
}
.hse-BadgeIcon_variant_clickable .hse-BadgeIcon__label_size_medium {
  @include text_caption_small_bold;
}
.hse-BadgeIcon_variant_clickable .hse-BadgeIcon__label_size_large {
  @include text_caption_medium_bold;
}
.hse-BadgeIcon_variant_clickable.hse-BadgeIcon_disabled {
  cursor: default;

  .hse-BadgeIcon__avatar::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background: rgba($color-element-overlay-dark, $opacity-40);
  }

  .hse-BadgeIcon__label {
    color: $color-gray-dusk-3;
  }

  &:hover {
    background-color: initial;
  }
}
