/// IconButton
/// В варианте default пользователь может менять цвета фон через свойство color
/// В вариантах ghost, pagination пользователь может менять фон только в состоянии selected

@import '../../base';
@import './vars';
@import './mixins';

.hse-IconButton {
  @include IconButton;
}

.hse-IconButton:focus-visible {
  @include focus-shadow;
}

.hse-IconButton.hse-IconButton_disabled {
  @include IconButton_disabled;
}

/// Sizes
.hse-IconButton.hse-IconButton_size_small {
  @include IconButton_size_small;
}

.hse-IconButton.hse-IconButton_size_medium {
  @include IconButton_size_medium;
}

.hse-IconButton.hse-IconButton_size_large {
  @include IconButton_size_large;
}

/// Bubble style
.hse-IconButton.hse-IconButton_variant_ghost:not(.hse-IconButton_selected)
  .hse-Button__bubble,
.hse-IconButton.hse-IconButton_variant_pagination:not(.hse-IconButton_selected)
  .hse-Button__bubble {
  background: $color-brand-5;
}

/// Variant 'default' styles
.hse-IconButton_variant_default .hse-IconButton__content {
  color: $color-base-white;
}

.hse-IconButton_variant_default.hse-IconButton_inactive
  .hse-IconButton__content {
  color: $color-gray-dusk-4;
}

.hse-IconButton_variant_default.hse-IconButton_spinner::before {
  content: none;
}

.hse-IconButton.hse-IconButton_variant_default.hse-IconButton_spinner {
  background: $color-gray-morn-3;
}

.hse-IconButton.hse-IconButton_variant_default.hse-IconButton_spinner_done {
  background: $color-brand;
}

/// Spinner styles

.hse-IconButton__spinner {
  --checkmark-fill-box-shadow: none;
  --spinner-color: #{$color-brand};
  --spinner-color-hover: #{$color-brand};
  color: $color-brand;
}

.hse-IconButton__spinner_done {
  --spinner-color: #{$color-base-white};
  --spinner-color-hover: #{$color-base-white};
  color: $color-base-white;
}

.hse-IconButton__spinner_done .hse-Spinner__overlay {
  stroke: transparent;
}

/// Variant 'ghost' styles

.hse-IconButton.hse-IconButton_variant_ghost {
  background: transparent;
}

.hse-IconButton.hse-IconButton_variant_ghost .hse-IconButton__content {
  color: $color-brand;
}

.hse-IconButton.hse-IconButton_disabled.hse-IconButton_variant_ghost {
  background: transparent;
}

.hse-IconButton.hse-IconButton_disabled.hse-IconButton_variant_ghost
  .hse-IconButton__content {
  color: $color-brand-4;
}

/// selected styles

.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_ghost,
.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_pagination,
.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_secondary {
  color: $color-brand;
  background: currentColor;

  &:hover {
    &::before {
      background: rgba($color-element-overlay-light, $opacity-40);
    }
  }
}

.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_ghost
  .hse-IconButton__content {
  color: $color-base-white;
}

.hse-IconButton.hse-IconButton_focused:not(.hse-IconButton_selected).hse-IconButton_variant_ghost {
  background: transparent;
}

.hse-IconButton.hse-IconButton:hover:not(.hse-IconButton_inactive):not(.hse-IconButton_disabled):not(.hse-IconButton_selected).hse-IconButton_variant_ghost {
  background: $color-gray-morn-3;
}

.hse-IconButton.hse-IconButton_variant_ghost.hse-IconButton:hover:not(.hse-IconButton_inactive):not(.hse-IconButton_disabled):not(.hse-IconButton_selected)
  .hse-IconButton__content {
  color: $color-brand;
}

.hse-IconButton.hse-IconButton_inactive.hse-IconButton_variant_ghost {
  background: $color-gray-morn-4;
}

.hse-IconButton.hse-IconButton_inactive.hse-IconButton_variant_ghost
  .hse-IconButton__content {
  color: $color-gray-dusk-4;
}

/// Variant 'pagination' styles

.hse-IconButton.hse-IconButton_variant_pagination .hse-IconButton__content {
  color: $color-base-black;
}

.hse-IconButton.hse-IconButton_variant_pagination .hse-IconButton__content {
  color: $color-gray-dusk;
}

.hse-IconButton.hse-IconButton_disabled.hse-IconButton_variant_pagination
  .hse-IconButton__content {
  color: $color-gray-dusk-4;
}

.hse-IconButton.hse-IconButton:hover:not(.hse-IconButton_inactive):not(.hse-IconButton_disabled):not(.hse-IconButton_selected).hse-IconButton_variant_pagination {
  background: $color-gray-morn-3;
}

.hse-IconButton.hse-IconButton_inactive.hse-IconButton_variant_pagination {
  background: $color-gray-morn-4;
}

.hse-IconButton.hse-IconButton_inactive.hse-IconButton_variant_pagination
  .hse-IconButton__content,
.hse-IconButton.hse-IconButton_disabled.hse-IconButton_variant_pagination
  .hse-IconButton__content {
  color: $color-gray-dusk-4;
}

.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_pagination
  .hse-IconButton__content {
  color: $color-base-white;
}

/// Variant 'secondary' styles

.hse-IconButton.hse-IconButton_variant_secondary {
  @include IconButton_variant_secondary;
}

.hse-IconButton.hse-IconButton_variant_secondary:hover:not(.hse-IconButton_inactive):not(.hse-IconButton_disabled):not(.hse-IconButton_selected) {
  @include IconButton_variant_secondary_hover;
}

.hse-IconButton.hse-IconButton_variant_secondary .hse-IconButton__content {
  @include IconButton_variant_secondary__content;
}

.hse-IconButton.hse-IconButton_disabled.hse-IconButton_variant_secondary {
  @include IconButton_variant_secondary_disabled;
}

.hse-IconButton.hse-IconButton_inactive.hse-IconButton_variant_secondary {
  @include IconButton_variant_secondary_inactive;
}

.hse-IconButton.hse-IconButton_selected.hse-IconButton_variant_secondary {
  @include IconButton_variant_secondary_selected;
}
