@import '../base';

.hse-Error {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.hse-Error__title {
  @include text-caption_medium_bold;
  color: $color-base-black;
}

.hse-Error__description {
  @include text_paragraph_small;
  margin: $spacing-small-x 0 0;
  color: $color-base-black;
}

.hse-Error__button {
  display: flex;
  justify-content: center;
  margin: $spacing-small 0 0;
}
