@import '../base';

.hse-BaseFooter {
  width: 100%;
  padding: $spacing-medium-x 0 $spacing-small;
  color: $color-text-base-white;
  background: $color-brand-dark;
}

.hse-BaseFooter .col-sidebar {
  @include respond-down('tablet-small') {
    display: none; // todo: conditional rendering
  }
}

.hse-Footer {
}

.hse-Footer__menu {
  position: relative;
}

.hse-Footer__menu {
  @include respond('mobile-small') {
    margin: 0 0 $spacing-medium;
  }
}

.hse-Footer__menu:nth-last-of-type(n + 2) {
  @include respond('tablet-small') {
    margin: 0 0 $spacing-large-2x/2;
  }

  @include respond('mobile-large') {
    margin: 0 0 $spacing-large-2x/2;
  }
}

.hse-BaseFooter:not(.hse-BaseFooter_hasSidebar)
  .hse-Footer__menu:nth-of-type(2n)
  .hse-Footer__menuDivider {
  @include respond('tablet-small') {
    display: none; // todo: conditional rendering
  }
}

.hse-BaseFooter_hasSidebar
  .hse-Footer__menu:nth-of-type(2n + 1)
  .hse-Footer__menuDivider {
  @include respond('tablet-small') {
    display: none; // todo: conditional rendering
  }
}

.hse-Footer__menu .hse-Footer__menuDivider {
  @include respond-down('mobile-large') {
    display: none; // todo: conditional rendering
  }
}

.hse-Footer__menuTitle {
  @include text_caption_large_bold;
  color: $color-text-base-white;
}

.hse-Footer__menuItems {
  margin: $spacing-small-x 0 0 0;
  padding: 0;
  list-style-type: none;
}

.hse-Footer__menuItem {
  @include text_caption_medium_regular;
  color: rgba($color-text-base-white, $opacity-60);
}

.hse-Footer__menuItem:not(:last-child) {
  margin: 0 0 $spacing-small-2x 0;
}

.hse-Footer__menuItemLink {
}

.hse-Footer__divider {
  background: rgba($color-background-white, $opacity-10);
}

.hse-Footer__divider_horizontal {
  margin: $spacing-large 0 $spacing-small;
  @include respond('mobile-small') {
    margin: $spacing-small-x 0 $spacing-small;
  }
}

.hse-Footer__info {
  display: flex;
  width: 100%;
  @include respond-up('tablet-large') {
    align-items: center;
  }
  @include respond-down('tablet-small') {
    flex-direction: column;
  }
}

.hse-Footer__copyright {
  @include text_caption_large_bold;
}

.hse-Footer__socialLinks {
  display: flex;
  @include respond-up('tablet-large') {
    margin-left: auto;
  }
  @include respond-down('tablet-small') {
    margin: $spacing-small 0 0;
  }
}

.hse-Footer__socialLink.hse-ClickableIcon {
  padding: $spacing-small-3x / 2;
}

.hse-Footer__socialLink:not(:last-child) {
  margin-right: $spacing-small-3x;
}

.hse-Footer__menuDivider {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
}
