@import '../base';

$rt-toast-width: auto;
$rt-toast-background: transparent;
$rt-toast-min-height: auto;

$rt-mobile: 'only screen and (max-width: 0)';

@import '~react-toastify/scss/variables';
@import '~react-toastify/scss/toast';
@import '~react-toastify/scss/progressBar';

// entrance and exit animations
@import '~react-toastify/scss/animations/bounce.scss';
@import '~react-toastify/scss/animations/zoom.scss';
@import '~react-toastify/scss/animations/flip.scss';
@import '~react-toastify/scss/animations/slide.scss';

.Toastify__toast {
  min-width: 296px;
  max-width: 1216px;
  margin-bottom: $spacing-small-x !important;
  min-height: auto !important;
  padding: 0 !important;
  background: transparent !important;
  box-shadow: none !important;
  border-radius: $radius-small !important;
  pointer-events: auto;
  cursor: initial;
  user-select: none;
}

// In case developer didn't pass { hideProgressBar: true } to options
.Toastify__progress-bar {
  // При display: none не работает autoClose
  // @see https://github.com/fkhadra/react-toastify/issues/320
  opacity: 0 !important;
  pointer-events: none !important;
  visibility: hidden !important;
}

.Toastify .hse-BaseAlert__closeButton {
  position: absolute;
  top: $spacing-small-x;
  right: $spacing-small-x;
}

// ToastContainer styles
$toast-container-padding: $spacing-small-3x;

.Toastify__toast-container {
  z-index: $z-index-toast;
  transform: translate3d(0, 0, 9999px);
  position: fixed;
  width: $rt-toast-width;
  box-sizing: border-box;
  color: #fff;
  pointer-events: none;
  padding: 0 0 0 $spacing-small-x;

  &--top-left {
    top: $toast-container-padding;
    left: $toast-container-padding;
  }
  &--top-center {
    top: $toast-container-padding;
    left: 50%;
    transform: translateX(-50%);
  }
  &--top-right {
    top: $toast-container-padding;
    right: $toast-container-padding;
  }
  &--bottom-left {
    bottom: $toast-container-padding;
    left: $toast-container-padding;
  }
  &--bottom-center {
    bottom: $toast-container-padding;
    left: 50%;
    transform: translateX(-50%);
  }
  &--bottom-right {
    bottom: $toast-container-padding;
    right: $toast-container-padding;
  }
}
