@import '../base';
@import '../baseText';

.hse-Tag {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  vertical-align: middle;
  color: $color-brand;
  border: none;
  border-radius: $radius-small;
  background-color: rgba($color-brand, $opacity-10);

  &:focus-visible:not(.hse-Tag_no-focus),
  &_focused {
    color: $color-base-white;
    background-color: $color-brand-dark;
    box-shadow: 0 0 0 2px rgba($color-brand, $opacity-40);
  }
  &:focus {
    border: none;
    outline: none;
  }
}

.hse-Tag_hovered {
  color: $color-base-white;
  background-color: $color-brand-dark;
}

.hse-Tag_focused {
  outline: none;
}

.hse-Tag_clickable:not(.hse-Tag_disabled) {
  cursor: pointer;
  &:hover {
    color: $color-base-white;
    background-color: $color-brand-dark;
  }
  &:active {
    color: $color-base-white;
    background-color: $color-brand;
  }

  &:focus {
    outline: none;
  }
}

.hse-Tag_size_small {
  @include text_label_x_small_bold;
  height: $input-size-large/2;
  padding: 6px 8px;
}
.hse-Tag_size_medium {
  @include text_caption_small_bold;
  height: $input-size-small;
  padding: 6px 8px;
}

.hse-Tag_disabled {
  color: rgba($color-gray-dusk, $opacity-60);
  border: 1px solid rgba($color-gray-dusk, $opacity-60);
  background-color: $color-base-white;
  &:hover {
    color: rgba($color-gray-dusk, $opacity-60);
    border: 1px solid rgba($color-gray-dusk, $opacity-60);
    background-color: $color-base-white;
  }
}

.hse-Tag__icon {
  text-align: center;
  vertical-align: -0.125em;
  text-transform: none;
  color: inherit;
  font-style: normal;
  line-height: 0;
}
.hse-Tag__icon_left {
  margin-right: $spacing-small-3x / 2;
}
.hse-Tag__icon_right {
  margin-left: $spacing-small-3x / 2;
}

.hse-Tag_selected {
  color: $color-base-white;
  background-color: $color-brand;
}
