@import '../../base';
@import './mixins';

.hse-TextArea {
  @include TextArea;
}

.hse-TextArea_autosize {
}

.hse-TextArea__textarea {
  @include TextArea__textarea;
}

.hse-TextArea__textarea_autosize {
  resize: none;
}

.hse-TextArea__textarea_disabled {
  resize: none;

  &,
  &::placeholder {
    color: $color-gray-dusk-4;
  }
}

.hse-TextArea__textarea_hasCounter {
  $padding-bottom: $line-height-12 + $spacing-small-3x * 2;
  padding-bottom: $padding-bottom;
  scroll-padding-bottom: $padding-bottom;
}

.hse-TextArea__counter {
  @include text_label_x_small_regular;
  position: absolute;
  right: $scrollbar-width;
  bottom: 0;
  left: 0;
  padding: $spacing-small-3x $spacing-small-2x $spacing-small-3x
    $spacing-small-2x;
  user-select: none;
  pointer-events: none;
  color: $color-gray-dusk-3;
  border-radius: $radius-small;
  background: $color-background-white;
}

.hse-TextArea__counter_error {
  color: $color-system-error;
}

.hse-TextArea_disabled .hse-TextArea__counter {
  background: $color-gray-morn-3;
}
