@mixin reset-button {
  align-items: flex-start;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  cursor: default;
  text-align: center;
  text-indent: 0;
  letter-spacing: inherit;
  word-spacing: inherit;
  text-transform: none;
  color: inherit;
  border: none;
  outline: none;
  background: none;
  text-shadow: none;
  font: inherit;
  line-height: inherit;
  text-decoration: none;

  &:focus {
    outline: none;
  }
}
