@import '../../base';

@mixin Checkbox {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  vertical-align: middle;

  &,
  &:focus {
    outline: none;
  }
}

@mixin Checkbox_inactive {
  cursor: default;
}

@mixin Checkbox__label_inactive {
  color: $color-gray-dusk-4;
}

@mixin Checkbox__label {
  @include text_caption_medium_regular;
  display: inline-flex;
  margin-left: $spacing-small-3x;
  transition: color $transition-quick ease-out;
  vertical-align: middle;
  color: $color-base-black;
}

@mixin Checkbox_readonly {
  cursor: not-allowed;
}
