@import '../../base';

@mixin ol($li-selector: '.hse-Li') {
  & {
    @include text_paragraph_large;
    margin: 0;
    padding: 0;
    list-style-position: inside;
  }

  & > & {
    margin-top: $em16;
    margin-bottom: $em16;
    margin-left: $spacing-small;
  }

  #{$li-selector} {
    padding-left: $em6;
  }
}

@mixin ol_withShift {
  @include respond-up('tablet-large') {
    margin-left: $spacing-large-x;
  }
}

@mixin ol_hasMargin {
  margin-top: $spacing-small;
  margin-bottom: $spacing-small;
  @include respond-down('desktop-small') {
    margin-top: $spacing-small-x;
    margin-bottom: $spacing-small-x;
  }
}

@mixin ul($li-selector: '.hse-Li') {
  & {
    @include text_paragraph_large;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  & > & {
    margin-top: $em16;
    margin-bottom: $em16;
    margin-left: #{$spacing-small};
  }

  & #{$li-selector} {
    position: relative;
    padding-left: $spacing-small;

    &::before {
      position: absolute;
      top: 0.2em;
      left: 0;
      display: inline-block;
      width: 1em;
      height: 1em;
      content: '';
      background-image: url('./assets/images/listBullet.svg');
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 8px;
    }
  }
}

@mixin ul_withShift {
  @include respond-up('tablet-large') {
    margin-left: $spacing-medium-x;
  }
}

@mixin ul_hasMargin {
  margin-top: $spacing-small;
  margin-bottom: $spacing-small;
  @include respond-down('desktop-small') {
    margin-top: $spacing-small-x;
    margin-bottom: $spacing-small-x;
  }
}

@mixin li(
  $ul-selector: '.hse-Ul',
  $ol-selector: '.hse-Ol',
  $p-selector: '.hse-Paragraph'
) {
  & {
    margin-top: 0;
    /// Стандартный отступ между элементами
    margin-bottom: $em16;
  }

  /// Если в элементе списка (li) используются параграфы, то отступы изменяются
  /// Если есть больше двух параграфов, отступы со второго до предпоследнего параграфа следующие
  & #{$p-selector} {
    margin-top: 0;
    margin-bottom: $em16;
  }

  /// Если есть несколько элементов, то у первого параграфа следующий отступ
  & #{$p-selector}:first-child:not(:last-child) {
    margin-bottom: $em8;
  }

  /// Если есть несколько элементов, то у последнего параграфа следующий отступ
  & #{$p-selector}:last-child:not(:first-child) {
    margin-bottom: $em24;
  }

  & #{$ul-selector},
  & #{$ol-selector} {
    margin-top: $em16;
    margin-bottom: 0;
    margin-left: 0;
  }

  & #{$ol-selector} {
    margin-left: 1em;
  }

  /// для корректного отображения номера пункта
  & #{$p-selector}:first-child {
    display: inline-block;
  }

  & #{$p-selector} ~ #{$ol-selector},
  & #{$p-selector} ~ #{$ul-selector} {
    margin-top: $em8;
  }
}

@mixin h1_adaptive {
  @include respond('tablet-large') {
    @include text_heading_h2;
  }
  @include respond('tablet-small') {
    @include text_heading_h2;
  }
  @include respond('mobile-large') {
    @include text_heading_h3;
  }
  @include respond('mobile-small') {
    @include text_heading_h3;
  }
}

@mixin h2_adaptive {
  @include respond('tablet-large') {
    @include text_heading_h3;
  }
  @include respond('tablet-small') {
    @include text_heading_h3;
  }
  @include respond('mobile-large') {
    @include text_heading_h4;
  }
  @include respond('mobile-small') {
    @include text_heading_h4;
  }
}

@mixin h3_adaptive {
  @include h2_adaptive;
}

@mixin h1_hasMargin {
  margin: 0 0 $spacing-small 0;
}

@mixin h2_hasMargin {
  margin: $spacing-large-2x + $spacing-small-3x 0 $spacing-small 0;
}

@mixin h3_hasMargin {
  margin: $spacing-medium-x 0 $spacing-small-x 0;
}

@mixin h4_hasMargin {
  margin: $spacing-medium-x 0 $spacing-small-x 0;
}
