@import '../../base';
@import '../BadgeLabel/BadgeLabelMixins';
@import './SidebarItemMixins';

.hse-SidebarItem {
  @include reset-button;
  --background-color: #{$color-background-white};
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: $sidebar-icon-size;
  cursor: pointer;
  transition: background-color $transition-quick ease-out;
  color: $color-dusk;
  background: var(--background-color);
}

.hse-SidebarItem::before {
  position: absolute;
  z-index: 2;
  top: $spacing-small-x;
  bottom: $spacing-small-x;
  left: 0;
  width: 2px;
  content: '';
  transition: background-color $transition-quick ease-out;
  background: var(--background-color);
}

.hse-SidebarItem:hover,
.hse-SidebarItem:hover .hse-SidebarItem__icon .hse-Icon,
.hse-SidebarItem_selected,
.hse-SidebarItem_selected .hse-SidebarItem__icon .hse-Icon {
  color: $color-brand;
}

.hse-SidebarItem:focus-visible,
.hse-SidebarItem:focus-visible .hse-SidebarItem__icon .hse-Icon {
  color: $color-brand;
}

/// Modifiers

.hse-SidebarItem_selected::before {
  background: $color-brand;
}

.hse-SidebarItem_hasIcon .hse-SidebarItem__right {
  padding-right: $spacing-small-x;
}

/// Elements

.hse-SidebarItem__icon {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  padding: $spacing-small;
  transition: background-color $transition-quick;
  background: linear-gradient(
    90deg,
    var(--background-color) 0%,
    var(--background-color) 80%,
    rgba(255, 255, 255, 0) 100%
  );
}

.hse-SidebarItem_iconBadge {
  position: absolute;
  top: $spacing-small-x;
  left: calc(50% + 5px);
  pointer-events: none;
}

.hse-SidebarItem__right {
  position: relative;
  display: flex;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 0;
  justify-content: space-between;
  width: calc(var(--sidebar-width) - #{$sidebar-icon-size});
  padding-left: $spacing-small;
  transition: opacity 0.15s 0.05s ease, transform $transition-quick;
}

.hse-SidebarItem__title {
  @include text_caption_large_bold;
  text-align: left;

  @include respond-down('desktop-medium') {
    @include text_caption_medium_bold;
  }
}

.hse-SidebarItem__icon + .hse-SidebarItem__right {
  padding-left: 0;
}

.hse-SidebarItem__labelContainer {
  flex: 0 0 72px;
  margin: 0 0 0 auto;
  padding-left: $spacing-small-3x;
  text-align: left;
  @include respond-down('desktop-small') {
    flex: 0 0 $sizing-64;
  }
}

.hse-SidebarItem__label {
  cursor: pointer;
  @include respond-down('tablet-large') {
    @include BadgeLabel_size_small;
  }
}

.hse-SidebarItem_size_tight {
  min-height: $sizing-56;

  &::before {
    $padding: ($sizing-56 - $sizing-40) / 2;
    top: $padding;
    bottom: $padding;
  }
}

.hse-SidebarItem_size_tight .hse-SidebarItem__icon {
  padding: $spacing-small-x $spacing-small;
}

.hse-SidebarItem_variant_static .hse-SidebarItem__right {
  padding-right: $spacing-small;
}
.hse-SidebarItem_variant_static .hse-SidebarItem__labelContainer {
  margin-right: -1 * $spacing-small;
}

.hse-SidebarItem .hse-Tooltip {
  pointer-events: none;
}

.hse-SidebarItem__tooltipContent {
  white-space: nowrap;
}
