@import '../../base';
@import './vars';

@mixin hse-Tabs {
  display: flex;
  align-items: center;
}

@mixin hse-TabsItem {
  @include reset-default-input;
  @include text-caption-medium-bold;
  position: relative;
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  height: $tabs-item-height;
  padding: $spacing-small-2x $spacing-small-x;
  cursor: pointer;
  transition: box-shadow $transition-quick ease-out;
  vertical-align: middle;
  color: $color-gray-dusk;

  &:focus-visible::before {
    @include focus-shadow;
  }

  &::before {
    content: '';
    z-index: 1;
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
  }
}
@mixin hse-Tabs__TabsItem {
  &:not(:last-child) {
    margin-right: $spacing-small-3x;
  }
}
@mixin hse-Tabs_variant_line__TabsItem {
  &:not(:last-child) {
    margin-right: 0;
  }
}
@mixin hse-TabsItem__counter {
  margin-left: $spacing-small-3x;
  pointer-events: none;
}

@mixin hse-TabsItem_selected {
  color: $color-base-white;
  background-color: $color-brand;
}

@mixin hse-TabsItem_disabled {
  cursor: default;
  color: $color-gray-morn;
}

@mixin hse-TabsItem_variant_line {
  border-radius: 0;

  &:focus-visible {
    color: $color-brand;
    background: inherit;

    &::after {
      background: $color-brand;
    }
  }

  &::after {
    position: absolute;
    background: $color-gray-morn;
    z-index: 1;
    right: 0;
    bottom: -1px;
    left: 0;
    height: 2px;
    content: '';
    transition: background $transition-quick ease-out;
  }
}

@mixin hse-TabsItem_variant_line_selected {
  color: $color-brand;
  background-color: inherit;
  &::after {
    background: $color-brand;
  }
}

@mixin hse-TabsItem_variant_line_hover {
  &::after {
    background: $color-brand;
  }
}
