@import '../../base';

.hse-SwitcherIcon {
  $width: $sizing-40;
  position: relative;
  display: flex;
  flex: 0 0 $width;
  width: $width;
  height: 20px;
  border-radius: $radius-large;
  background-color: $color-gray-morn;

  &::after {
    position: absolute;
    top: $spacing-small-3x / 2;
    left: $spacing-small-3x / 2;
    width: $sizing-24 / 2;
    height: $sizing-24 / 2;
    content: '';
    transition: left $transition-quick;
    border-radius: $radius-medium;
    background-color: $color-base-white;
    box-shadow: 0 1px 2px rgba(0, 0, 0, $opacity-10),
      0 4px 12px rgba(0, 0, 0, $opacity-10);
  }
}

.hse-SwitcherIcon_checked {
  background-color: $color-brand;

  &::after {
    left: $spacing-small;
  }
}

.hse-SwitcherIcon_focused {
  @include focus-shadow;
}

.hse-SwitcherIcon_focused.hse-SwitcherIcon_checked {
  background-color: $color-brand-dark;
}

.hse-SwitcherIcon_inactive {
  background: $color-gray-morn-3;
  box-shadow: none;

  &::after {
    box-shadow: none;
  }
}

.hse-SwitcherIcon_checked.hse-SwitcherIcon_inactive {
  background: $color-brand-4;
  &::after {
    box-shadow: 0 1px 2px rgba(0, 0, 0, $opacity-10),
      0 4px 12px rgba(0, 0, 0, $opacity-10);
  }
}

.hse-SwitcherIcon_size_medium {
  $width: 56px;
  flex: 0 0 $width;
  width: $width;
  height: 28px;
  border-radius: 14px;

  &::after {
    top: $spacing-small-2x / 2;
    left: $spacing-small-2x / 2;
    width: $sizing-16;
    height: $sizing-16;
    border-radius: $radius-medium;
  }
}

.hse-SwitcherIcon_checked.hse-SwitcherIcon_size_medium {
  &::after {
    left: 34px;
  }
}
