// @define Table

@import '../../base';
@import './vars';
@import './mixins';

.hse-Table {
  overflow-x: auto;
  width: auto;
}

.hse-Table_hasMargin {
  @include respond-up('desktop-small') {
    margin: $spacing-40 0;
  }

  @include respond-up('tablet-small') {
    margin: $spacing-medium 0;
  }

  @include respond-down('mobile-large') {
    padding: $spacing-small 0;
  }
}

.hse-Table__table {
  @include Table__table;
}

.hse-Table__thead {
  @include Table__thead;
}

.hse-Table__tfoot {
  @include Table__tfoot;
}

.hse-Table__th {
}

.hse-Table__tr {
  @include Table__tr;
}

.hse-Table__td {
  @include Table__td;
}

.hse-Table__td_align_left {
  text-align: left;
}

.hse-Table__td_align_center {
  text-align: center;
}

.hse-Table__td_align_right {
  text-align: right;
}

.hse-Table__thead .hse-Table__td {
  @include Table__thead__td;
}
