// @define BaseCheckboxIcon

@import '../../base';
@import './CheckboxIconMixins';

.hse-BaseCheckboxIcon {
  position: relative;
  display: flex;
  align-items: center;
  flex: 0 0 $sizing-16;
  justify-content: center;
  box-sizing: border-box;
  width: $sizing-16;
  height: $sizing-16;
  transition: $transition-quick background-color ease-out,
    $transition-quick border-color ease-out;
  border: 1px solid $color-gray-dusk-3;
  border-radius: $radius-small;
  background-color: $color-background-white;
}

.hse-BaseCheckboxIcon__icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hse-BaseCheckboxIcon__icon > * {
  transition: $transition-quick opacity ease-out,
    $transition-quick color ease-out;
  opacity: 0;
  color: $color-base-white;
}

.hse-BaseCheckboxIcon__icon_visible > * {
  opacity: 1;
}

.hse-BaseCheckboxIcon__icon_inactive > * {
  color: $color-gray-dusk-4;
}

.hse-BaseCheckboxIcon_checked {
  border-color: transparent;
  background-color: $color-brand;
}

.hse-BaseCheckboxIcon_inactive {
  @include CheckboxIcon_inactive;
}

.hse-BaseCheckboxIcon_focused {
  @include CheckboxIcon_focus;
}

.hse-BaseCheckboxIcon_radio {
  border-radius: 50%;
}

.hse-BaseCheckboxIcon_checked.hse-BaseCheckboxIcon_focused {
  @include CheckboxIcon_checked_focus;
}

.hse-BaseCheckboxIcon_radio.hse-BaseCheckboxIcon_checked {
  border: 6px solid $color-brand;
  background-color: transparent;
}

.hse-BaseCheckboxIcon_radio.hse-BaseCheckboxIcon_checked.hse-BaseCheckboxIcon_inactive {
  border: 6px solid $color-gray-morn-2;
  background-color: transparent;
}

.hse-BaseCheckboxIcon_radio.hse-BaseCheckboxIcon_checked.hse-BaseCheckboxIcon_focused {
  @include CheckboxIcon_radio_checked_focus;
}
