// @define Space

@import '../base';
@import '../functions';
@import '~@hse-design/tokens/lib/scss/spacing.map';

.hse-Space {
}

.hse-Space_horizontal {
  @each $name, $value in $spacing-map {
    $name: str-replace($name, 'spacing-', '');
    $name: str-replace($name, '-', '_');

    &.hse-Space_size_#{$name} {
      width: $value;
      min-width: $value;
    }
  }
}

.hse-Space_vertical {
  @each $name, $value in $spacing-map {
    $name: str-replace($name, 'spacing-', '');
    $name: str-replace($name, '-', '_');

    &.hse-Space_size_#{$name} {
      height: $value;
      min-height: $value;
    }
  }
}
