@import '../../base';
@import './SidebarItemMixins';

.hse-Sidebar {
  position: fixed;
  z-index: $z-index-sidebar;
  top: 0;
  bottom: 0;
  left: 0;
  display: block;
  /* stylelint-disable-next-line plugin/no-low-performance-animation-properties */
  transition: width $transition-quick ease, transform $transition-quick ease;
  background: $color-background-white;
  box-shadow: #{$shadow-hover};
}

.hse-Sidebar_open {
  box-shadow: 0 0px 0px rgba(106, 122, 152, 0.15);
  border-right: 1px solid $color-gray-morn-3;
}

.hse-Sidebar__wrapper {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

/// Modifiers

.hse-Sidebar_withHeader {
  top: $header-height;

  @include respond-down('mobile-large') {
    top: $header-height-small;
  }
}

.hse-Sidebar_variant_dynamic {
  --sidebar-width: #{$sidebar-width-desktop-medium-fixed};
  width: $sidebar-width-desktop-medium-fixed;

  @include respond-down('desktop-small') {
    --sidebar-width: #{$sidebar-width-desktop-small-fixed};
    width: $sidebar-width-desktop-small-fixed;
  }
}

.hse-Sidebar_variant_static {
  --sidebar-width: #{$sidebar-width-desktop-small-static};
  width: $sidebar-width-desktop-small-static;
}

// Анимируем только иконку у кнопки
.hse-Sidebar_variant_dynamic .hse-Sidebar__toggleIcon {
  transition: transform $transition-quick ease-out;
  transform: rotateZ(360deg);
}

.hse-Sidebar_variant_dynamic.hse-Sidebar_open .hse-Sidebar__toggleIcon {
  transform: rotateZ(180deg);
}

.hse-Sidebar_variant_dynamic .hse-Sidebar__header {
  position: sticky;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: $sidebar-icon-size;
  background: $color-background-white;

  @include respond-down('tablet-small') {
    display: none;
  }
}

.hse-Sidebar_variant_dynamic.hse-Sidebar_size_tight .hse-Sidebar__header {
  height: $sizing-56;
}

.hse-Sidebar_variant_dynamic .hse-Sidebar__toggleContainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  padding: 0 0 0 $spacing-small-x;
}

/// Elements

.hse-Sidebar__toggleButton {
  flex-shrink: 0;
}

.hse-Sidebar__toggleText {
  @include text_caption_large_bold;
  margin: 0 0 0 $spacing-small-x;
  opacity: 0;
  white-space: nowrap;
  transition: opacity ease-out $transition-quick;
  cursor: pointer;
  color: $color-gray-dusk-1;

  @include respond-down('desktop-medium') {
    @include text_caption_medium_bold;
  }
}

.hse-Sidebar_open .hse-Sidebar__toggleText {
  opacity: 1;
}

.hse-Sidebar_size_tight .hse-Sidebar__toggleText {
  @include respond-down('desktop-medium') {
    display: none;
  }
}

.hse-Sidebar_variant_dynamic:not(.hse-Sidebar_open) {
  @include respond-up('tablet-large') {
    width: $sidebar-icon-size;

    .hse-SidebarItem {
      @include SidebarItem_dynamic_closed;
    }

    .hse-SidebarCustomItem_autoHide {
      display: none;
    }
  }
}

.hse-Sidebar_variant_dynamic,
.hse-Sidebar_variant_static {
  &:not(.hse-Sidebar_open) {
    @include respond-down('tablet-small') {
      transform: translate(-100%, 0);
    }
  }
}

.hse-Sidebar_variant_dynamic .hse-Sidebar__content {
  margin-top: $spacing-medium;
  @include respond-down('tablet-small') {
    margin-top: $spacing-small;
  }
}

.hse-Sidebar_variant_dynamic.hse-Sidebar_size_tight .hse-Sidebar__content {
  margin-top: $spacing-small;
  @include respond-down('desktop-medium') {
    margin-top: 0;
  }
}

.hse-Sidebar:not(.hse-Sidebar_open) .hse-SidebarItem.hse-SidebarItem_hasIcon {
  height: 72px;
}

.hse-Sidebar.hse-Sidebar_size_tight:not(.hse-Sidebar_open)
  .hse-SidebarItem.hse-SidebarItem_hasIcon {
  height: $sizing-56;
}

.hse-Sidebar .hse-Divider {
  margin: $spacing-small-x 0;
}

.hse-Sidebar__title {
  @include text_caption_large_bold;
  margin-bottom: $spacing-medium + $spacing-small-3x;
  padding: $spacing-small;
}

.hse-Sidebar_variant_static:not(.hse-Sidebar_hasTitle) {
  padding-top: $spacing-small;
}

.hse-Sidebar_size_tight .hse-Sidebar__title {
  margin-bottom: $spacing-medium + $spacing-small-3x / 2;
  padding: $spacing-small-2x $spacing-small;
}
