@import '../../base';
@import './mixins';

.hse-Tooltip {
  @include Tooltip;
}

.hse-Tooltip.hse-Tooltip_visible {
  @include Tooltip_visible;
}

.hse-TooltipTarget {
  @include TooltipTarget;
}

.hse-Tooltip_visible,
.hse-TooltipTarget:hover ~ .hse-Tooltip,
.hse-TooltipTarget:focus ~ .hse-Tooltip {
  opacity: 1;
}

.hse-Tooltip_closable .hse-Tooltip__content {
  @include Tooltip_closable__content;
}

.hse-Tooltip__container {
  @include Tooltip__container;
}

.hse-Tooltip__content {
  @include Tooltip__content;
}

.hse-Tooltip__closeButton {
  @include Tooltip__closeButton;
}

.hse-Tooltip_variant_brand .hse-Tooltip__content,
.hse-Tooltip_variant_brand .hse-Tooltip__arrow {
  @include Tooltip_variant_brand;
}

.hse-Tooltip_variant_black .hse-Tooltip__content,
.hse-Tooltip_variant_black .hse-Tooltip__arrow {
  @include Tooltip_variant_black;
}

.hse-Tooltip_variant_invert .hse-Tooltip__content,
.hse-Tooltip_variant_invert .hse-Tooltip__arrow {
  @include Tooltip_variant_invert;
}
