@import '../../base';

@mixin Link_variant_primary($inactiveSelector: '.hse-Link_inactive') {
  &,
  &:visited {
    color: $color-base-black;
    border-bottom-color: $color-brand;

    &#{$inactiveSelector} {
      &,
      &:visited {
        color: $color-gray-dusk-3;
        border-bottom-color: currentColor;
      }
    }

    @include Link_hover {
      color: $color-brand;
      border-bottom-color: transparent;
    }

    @include Link_focus {
      color: $color-brand;
    }
  }
}

@mixin Link_hover {
  &:not(.hse-Link_inactive) {
    &:active,
    &:hover,
    &.hse-Link_hover {
      @content;
    }
  }
}

@mixin Link_focus {
  &:not(.hse-Link_inactive) {
    @include focus-visible {
      &:not(.hse-Link_controlledFocus) {
        @content;
      }
    }

    &.hse-Link_controlledFocus.hse-Link_focused {
      @content;
    }
  }
}

// Общие стили focused стейта для всех вариантов
@mixin Link_focused {
  @include Link_focus {
    @include focus-shadow;
    border-bottom-color: transparent;
    border-radius: $radius-small;
  }
}

// Общие стили для всех вариантов
@mixin Link {
  @include Link_focused;
  &.hse-Link_standalone {
    @include text_paragraph_large;
  }

  &,
  &:visited {
    cursor: pointer;
    transition: border-bottom-color $transition-quick ease-in-out,
      color $transition-quick ease-in-out;
    border-bottom: 1px solid transparent;

    &,
    &:hover,
    &:active,
    &:focus {
      text-decoration: none;
      outline: none;
    }
  }
}

@mixin Link_inactive {
  cursor: not-allowed;
}

@mixin Link_variant_primary_inactive {
  &,
  &:visited {
    color: $color-gray-dusk-3;
    border-bottom-color: currentColor;
  }
}
